import React, { PureComponent } from 'react';
import classnames from 'classnames';
import cls from './styles.module.scss';
import { UniRButton } from '../../../common';
import Img from 'react-image';

type UniRowProps = {
    className?: String,
    data?: Object,
    btnTitle?: String
}

class UniRow extends PureComponent<UniRowProps> {
    constructor(props) {
        super(props);
        this.state = {};
    }

    handleUniClick = () => {
console.log('a')
    }

    render() {
        let {
            className,
            data,
            btnTitle
        } = this.props;
        data.shortDescription =  data.shortDescription.toString().length > 200 
                    ? data.shortDescription.toString().substring(0,200)+'...' 
                    : data.shortDescription.toString();
        return (
            <div className={classnames(cls.uni, className)}>
                <div className={classnames(cls.uni_row)}>
                    <div className={classnames(cls.uni_logo_wrapper, "col-3")}>
                        <div className={classnames(cls.uni_logoContainer,"col-9")}>
                            {data.logo &&
                                <Img
                                    className={cls.uni_logo}
                                    src={data.logo}
                                />
                            }
                        </div>
                        
                    </div>
                    <div className={classnames(cls.uni_info, "col-7")}>
                        <div className={classnames("column")}>
                            <p className={cls.uni_title}>
                                <b>{data.title}</b>
                            </p>
                            <p>
                                {data.shortDescription}
                            </p>
                            <p>im chun: {data.mark}</p>
                        </div>
                    </div>
                    <div className={classnames(cls.uni_btn_wrapper, "col-2")}>
                        <UniRButton
                            title={btnTitle}
                            btnStyle={cls.uni_btn}
                            onClick={this.handleUniClick}
                        />

                    </div>
                </div>
            </div>
        );
    }
}

UniRow.defaultProps = {
    className: null,
    data: null,
    btnTitle: "Xem chi tit >>"
}

export default UniRow;