import React, { Component, Fragment } from 'react';
import LightBox from './LightBox';
import Img from 'react-image';
import cls from './styles.module.scss';
import classnames from 'classnames';
import UniRLoading from '../UniRLoading';
import { ERROR_IMAGE } from '../../appConfig';

type UniRImageProps = {
    className?: String,
    src?: Array | String,
    lightbox?: Boolean
}

class UniRImage extends Component<UniRImageProps> {
    constructor(props) {
        super(props);
        this.state = {
            isOpen: false
        };
    }

    onClick = (lightbox) => {
        if (lightbox) {
            this.setState({ isOpen: true })
        }
    }

    render() {
        let { isOpen } = this.state;
        let { className, lightbox, src, ...rest } = this.props;
        if (!Array.isArray(src) && src) {
            src = [src];
        } else {
            src = ERROR_IMAGE;
        }
        let isError = src === ERROR_IMAGE;
        return (
            <Fragment >
                <LightBox
                    isOpen={isOpen}
                    images={src}
                    onClose={() => this.setState({ isOpen: false })}
                />
                <div
                    className={classnames(cls.img_container, (lightbox && !isError) && cls.lightbox_container,)}
                    onClick={() => this.onClick(lightbox)}
                >
                    <span className={classnames(cls.footer)}></span>
                    <Img
                        className={classnames(
                            isError && cls.error_img,
                            (lightbox && !isError) && cls.lightbox,
                            className)}
                        loader={<UniRLoading />}
                        src={src}
                        {...rest}
                    />
                </div>
            </Fragment>
        );
    }
}

UniRImage.defaultProps = {
    className: null,
    src: [],
    lightbox: false
}

export default UniRImage;