import React, { Component } from 'react';
import cls from './styles.module.scss';
import classnames from 'classnames';
import { Modal } from 'antd';
import { UniRButton, UniRContainerHorizontal, UniRContainer } from '../../common';

type UniRModalProps = {
    children?: React.Node,
    title?: String,
    footer?: React.Node,
    visible?: Boolean,
    onOk?: Function,
    onCancel?: Function,
    loading?: Boolean,
    disabled?: Boolean,
    canceltittle?: String,
    oktitle?: String
}

class UniRModal extends Component<UniRModalProps> {
    constructor(props) {
        super(props);
        this.state = {};
    }

    handleOk = () => {
        this.props.onOk();
    }

    handleCancel = () => {
        this.props.onCancel();
    }

    render() {
        let {
            children,
            title,
            footer,
            visible,
            loading,
            disabled,
            canceltitle,
            oktitle
        } = this.props;

        canceltitle = canceltitle || "Hy";
        oktitle = oktitle || "Gi";
        footer = !footer ? footer = [
            <UniRContainerHorizontal
                key="0"
                className={classnames(cls.footer)}
            >
                <UniRContainer
                    nopadding
                    nomargin
                    className={classnames(cls.btn_container)}
                >
                    <UniRButton
                        btnStyle={classnames(cls.btn)}
                        key="back"
                        onClick={this.handleCancel}
                        >
                        {canceltitle}
                    </UniRButton>
                </UniRContainer>
                <UniRContainer
                    nopadding
                    marginhorrizontal
                    className={classnames(cls.btn_container)}
                >
                    <UniRButton
                        btnStyle={classnames(cls.btn)}
                        disabled={disabled}
                        key="submit"
                        type="primary"
                        loading={loading}
                        onClick={this.handleOk}
                    >
                        {oktitle}
                </UniRButton>
                </UniRContainer>
            </UniRContainerHorizontal>
        ] : footer;

        return (
            <Modal
                visible={visible}
                title={title}
                onOK={this.handleOk}
                onCancel={this.handleCancel}
                footer={footer}
                className={classnames(cls.modify)}
            >
                {children}
            </Modal>
        );
    }
}

UniRModal.defaultProps = {
    children: null,
    disabled: false,
    title: "",
    footer: null,
    visible: false,
    loading: false,
    canceltitle: "",
    oktitle: "",
    onOk: () => { },
    onCancel: () => { },
}

export default UniRModal;