import React, { Component } from 'react';
import cls from './styles.module.scss';
import classnames from 'classnames';
import { connect } from 'react-redux';
import {
    UniRPageLayout,
    UniRContainerHorizontal,
    UniRContainer,
    UniRContainerVertical,
    UniRInput,
    UniRButton,
    UniRDropdown,
    UniRLoading
} from '../../common';
import {
    Divider, Skeleton
} from 'antd';
import Tree from './Tree';
import UniRow from './UniRow';
import {
    getMajors,
    getCities,
    setMajors,
    searchUniversity,
    setCheckedMajors,
    setUniversities
} from '../../actions/uniSearch';
import { STATUS } from '../../constant';
import { isArray } from 'util';

const PAGE_SIZE = 1;

class UniSearch extends Component {
    constructor(props) {
        super(props);
        this.state = {
            universitySearch: '',
            pointFrom: '',
            pointTo: '',
            city: {},
            validPoint: "",
            cities: [],
            pageSize: PAGE_SIZE,
            pageIndex: 0,
            isSearch: true
        };
        this.tree = null;
    }

    componentDidMount() {

        this.props.getMajors('Major', "", 'MajorID', (data) => {
            this.reArrangeMajorsToTreeData(data);
        });
        this.props.getCities('City', "", 'CityID', (data) => {
            this.setState({
                city: {
                    key: data[0].CityID,
                    value: data[0].CityName
                },
                cities: data
            })
        });
        this.searchUniversity("", "", true)
    }

    reArrangeMajorsToTreeData = (data) => {
        let tree = []
        data.map((d) => {
            if (!d.MajorParentID) {
                let children = [];
                data.map((c) => {
                    if (c.MajorParentID === d.MajorID) {
                        children.push({
                            title: c.MajorName,
                            key: c.MajorID
                        });
                    }
                })

                tree.push({
                    title: d.MajorName,
                    key: d.MajorID,
                    children
                });
            }
        })
        this.props.setMajors(tree);
    }

    searchUniversity = (pageIndex = "", pageSize = "", isSearch = false) => {
        let {
            universitySearch,
            pointFrom,
            pointTo,
            city,
        } = this.state;
        pageIndex = pageIndex || this.state.pageIndex;
        pageSize = pageSize || this.state.pageSize;
        if ((isNaN(pointFrom) && pointFrom !== "") || (isNaN(pointTo) && pointTo !== "")) {
            this.setState({ validPoint: "Hy nhp mt s hp l!" })
        } else {
            let { checkedMajors, universities } = this.props.uniSearch;
            this.props.searchUniversity(
                checkedMajors,
                universitySearch,
                pointFrom,
                pointTo,
                city.key,
                (data) => {
                    isSearch ?
                        this.props.setUniversities(data) :
                        this.props.setUniversities(universities.concat(data));
                },
                pageIndex,
                pageSize
            );
        }

        this.setState({
            pageIndex: 0
        })
    }

    onChange = (key, value) => {
        if (key === 'pointFrom' || key === 'pointTo') {
            this.setState({
                validPoint: ""
            })
        }
        let state = this.state;
        state[key] = value;
        this.setState({
            ...state
        })
    }

    onSearch = () => {
        this.setState({
            isSearch: true
        });
        this.searchUniversity("", "", true);
    }

    reset = () => {
        let { cities } = this.state;
        let city = {};
        if (cities.length !== 0) {
            city = {
                key: 0,
                value: "Tt c"
            }
        }
        this.props.setCheckedMajors([])
        this.setState({
            universitySearch: '',
            pointFrom: '',
            pointTo: '',
            city,
            validPoint: ""
        })
    }

    onTagClick = (key, value) => {
        let state = this.state;
        if (isArray(key)) {
            key.map((k, i) => {
                state[k] = value[i];
            })
        } else {
            state[key] = value;
        }
        this.setState({
            ...state
        })
        this.onSearch();
    }

    showMore = () => {
        let { pageIndex } = this.state;
        this.setState({
            isSearch: false,
            pageIndex: pageIndex + 1
        })
        this.searchUniversity(pageIndex + 1);
    }

    isDefault = () => {
        let {
            universitySearch,
            pointFrom,
            pointTo,
            city,
        } = this.state;
        let { checkedMajors } = this.props.uniSearch;
        return (!universitySearch && !pointFrom && !pointTo && checkedMajors.length == 0 && (city.key == 0))
    }

    render() {
        let {
            universitySearch,
            pointFrom,
            pointTo,
            city,
            validPoint,
            isSearch,
        } = this.state;
        let {
            universities,
            cities,
            majors,
            getMajorsStatus,
            getCitiesStatus,
            totalUniversities,
            searchUniversityStatus
        } = this.props.uniSearch;
        let isDefault = this.isDefault();
        let getMajorsLoading = getMajorsStatus === STATUS.loading;
        let getCitiesLoading = getCitiesStatus === STATUS.loading;
        let searchUniversityLoading = searchUniversityStatus === STATUS.loading;
        let loading = <UniRLoading />;
        let unis = [];
        let btnShowMore = null;
        universities.length !== 0 ?
            universities.map((u) => {
                unis.push(
                    <UniRow
                        data={u}
                        key={u.UniversityID}
                        onTagClick={(key, value) => this.onTagClick(key, value)}
                    />
                )
            }) :
            unis = isSearch && unis.length === 0 && <UniRContainer>
                Khng tm thy kt qu no!
            </UniRContainer>
        btnShowMore = (searchUniversityLoading && isSearch) || (unis.length < totalUniversities &&
            <UniRContainer className={classnames(cls.btn_showmore_container)}>
                <UniRButton
                    containerStyle={classnames(cls.btn_showmore)}
                    title="Xem thm"
                    type="danger"
                    onClick={this.showMore}
                    loading={searchUniversityLoading}
                />
            </UniRContainer>
        )

        return (
            <UniRPageLayout>
                <UniRContainerHorizontal className={classnames(cls.container_unisearch)}>
                    <UniRContainer nomargin className="col-4" paddinghorrizontal>
                        <UniRContainerVertical>

                            <UniRContainer nomargin paddingvertical className={classnames(cls.header)}>
                                <UniRContainerHorizontal>
                                    <i className={classnames("fa fa-filter", cls.title_icon)}></i>
                                    <h2 className={classnames(cls.title_text)}>B lc tm kim</h2>
                                </UniRContainerHorizontal>
                            </UniRContainer>

                            <UniRContainer paddinghorrizontal nomargin>
                                <UniRContainerHorizontal>
                                    <UniRInput
                                        onChange={(value) => this.onChange("universitySearch", value)}
                                        containerStyle={classnames(cls.uni_search_input, "col-8")}
                                        placeholder="Trng i hc..."
                                        value={universitySearch}
                                    />
                                    <UniRContainer nomargin>
                                        <UniRButton
                                            btnStyle={classnames(cls.loading)}
                                            loading={searchUniversityLoading && isSearch}
                                            type="primary"
                                            onClick={this.onSearch}
                                            icon="search"
                                        />
                                    </UniRContainer>
                                </UniRContainerHorizontal>
                                <Divider
                                    className={classnames(cls.divider)}
                                    orientation="left"
                                >
                                    Khong im
                                </Divider>

                                <UniRInput
                                    error={validPoint}
                                    containerStyle={classnames(cls.group_container)}>
                                    <UniRInput.Group groupStyle={classnames(cls.uni_range_input)}>
                                        <UniRInput.Item
                                            placeholder="T"
                                            onChange={(value) => this.onChange("pointFrom", value)}
                                            value={pointFrom}
                                        />
                                        <UniRInput.Item
                                            placeholder="n"
                                            onChange={(value) => this.onChange("pointTo", value)}
                                            value={pointTo}
                                        />
                                    </UniRInput.Group>
                                </UniRInput>

                                {/* Khi ngnh */}
                                <Divider
                                    className={classnames(cls.divider)}
                                    orientation="left"
                                >
                                    Khi ngnh/Ngnh
                                </Divider>
                                {getMajorsLoading ?
                                    loading
                                    : <Tree
                                        data={majors}
                                        treeRef={(inst) => this.tree = inst}
                                    />}

                                {/* Thnh ph */}
                                <Divider
                                    className={classnames(cls.divider)}
                                    orientation="left"
                                >
                                    Thnh ph
                                </Divider>
                                <UniRDropdown
                                    containerStyle={classnames(cls.uni_search_input, cls.uni_range_input)}
                                    data={cities}
                                    dataid="CityID"
                                    datavalue="CityName"
                                    value={city.value}
                                    onClick={(item) => this.onChange("city", item)}
                                    loading={getCitiesLoading}
                                />
                                <UniRContainer nomargin>
                                    <UniRButton
                                        type="primary"
                                        onClick={this.reset}
                                        disabled={isDefault}
                                    >
                                        t li mc nh
                                    </UniRButton>
                                </UniRContainer>
                            </UniRContainer>
                        </UniRContainerVertical>
                    </UniRContainer>
                    <UniRContainer nomargin className="col-8">
                        <UniRContainerVertical>
                            <UniRContainer nomargin className="col-3" paddinghorrizontal>
                                <UniRButton
                                    btnStyle={classnames(cls.prevent_click)}
                                    size="large"
                                    title={`${totalUniversities} Kt qu`}
                                    tag
                                    type="primary" />
                            </UniRContainer>
                            {searchUniversityLoading && isSearch ? loading : unis}
                            {(searchUniversityLoading && !isSearch) && <Skeleton active />}
                        </UniRContainerVertical>
                        {btnShowMore}
                    </UniRContainer>
                </UniRContainerHorizontal>
            </UniRPageLayout>
        );
    }
}

const mapStateToProps = state => {
    return {
        uniSearch: state.uniSearch
    }
}

const mapDispatchToProps = dispatch => {
    return {
        getMajors: (entityName, where, order, callBackSuccess, pageNumber, rowsPerPage) => {
            dispatch(getMajors(entityName, where, order, callBackSuccess, pageNumber, rowsPerPage))
        },
        getCities: (entityName, where, order, callBackSuccess, pageNumber, rowsPerPage, ) => {
            dispatch(getCities(entityName, where, order, callBackSuccess, pageNumber, rowsPerPage))
        },
        setMajors: (majors) => {
            dispatch(setMajors(majors))
        },
        searchUniversity: (checkedMajors, universitySearch, pointFrom, pointTo, cityID, callBackSuccess, pageNumber, pageSize) => {
            dispatch(searchUniversity(checkedMajors, universitySearch, pointFrom, pointTo, cityID, callBackSuccess, pageNumber, pageSize))
        },
        setCheckedMajors: (majors) => {
            dispatch(setCheckedMajors(majors))
        },
        setUniversities: (universities) => {
            dispatch(setUniversities(universities))
        }
    }
}

export default connect(
    mapStateToProps,
    mapDispatchToProps
)(UniSearch);