import React, { PureComponent } from 'react';
import classnames from 'classnames';
import cls from './styles.module.scss';
import {
    UniRContainerVertical,
    UniRContainer
} from '../../../common';

type UniRFormLayoutProps = {
    title?: String,
    children?: React.Node,
    header?: boolean,
    body?: boolean,
    footer?: boolean
}

class UniRFormLayout extends PureComponent<UniRFormLayoutProps> {
    constructor(props) {
        super(props);
        this.state = {};
    }
    render() {
        let {
            title,
            children,
            header,
            body,
            footer
        } = this.props;
        let className = null;
        if (header) {
            className = cls.form_header_container;
        }
        if (body) {
            className = cls.form_body_container;
        }
        if (footer) {
            className = cls.form_footer_container;
        }
        return (
            <UniRContainerVertical
                className={className && classnames(className)}
            >
                {
                    (header && title) ?
                        <UniRContainer nomargin paddinghorrizontal>
                            <h2>{title}</h2>
                        </UniRContainer>
                        :
                        <UniRContainer nomargin>{children}</UniRContainer>
                }
            </UniRContainerVertical>
        );
    }
}

UniRFormLayout.defaultProps = {
    title: null,
    children: null,
    header: false,
    body: false,
    footer: false
}

export default UniRFormLayout;