import React, { PureComponent } from 'react';
import cls from './styles.module.scss';
import classnames from 'classnames';

type HeaderProps = {
    children?: React.Node,
    className?: String,
    divider?: React.node,
    title?: String
}

class Header extends PureComponent<HeaderProps> {
    constructor(props) {
        super(props);
        this.state = {};
    }
    render() {
        let {
            className,
            children,
            divider,
            title
        } = this.props;
        return (
            <div className={classnames(
                cls.header,
                divider || cls.divider_default,
                className)}
            >
                {title ?
                    <h2>
                        {title}
                    </h2>
                    : children}
                {divider}
            </div>
        );
    }
}

Header.defaultProps = {
    children: null,
    className: null,
    divider: null,
    title:  null
}

export default Header;