import React, { Component } from 'react';
import cls from './styles.module.scss';
import {
        UniRPageLayout,
        UniRContainerVertical,
        UniRContainerHorizontal,
        UniRImageGallery
        } from '../../common';
import classnames from 'classnames';
import Img from 'react-image';
import { API, STATUS } from '../../constant';
import { post} from '../../helper/axiosHelper';
import {
    Table
} from 'antd';
const columns = [{
    title: 'Ngnh',
    dataIndex: 'parentMajor',
    key: 'parentMajor',
    width: '30%'
  }, {
    title: 'Chuyn ngnh',
    dataIndex: 'childMajor',
    key: 'childMajor',
    width: '30%'
  }, {
    title: 'T hp mn',
    dataIndex: 'thm',
    width: '20%',
    key: 'thm',
  }, {
    title: 'im chun',
    dataIndex: 'point',
    key: 'point',
  }];
  
    const infoType1 = 'Khong im ';
    const infoType2 = 'T l th sinh c vic lm sau khi ra trng';
    const infoType3 = 'S nm o to';
    const diemchuanTitle = "Bng im chun";
    class UniDetail extends Component {
    constructor(props) {
        super(props);
        this.state = {
            universityId: 1,
            universityName: "",
            universityShortDescription: "",
            uniRangePoint: '10 ~ 20',
            universityJobAfterGraduating: 1,
            universityYearsAVG: 1,
            universityLongDescription: "",
            universityFeeAVG: 1,
            parentMajors: [],
            childMajors: [],
            data: [],

        }
    }
    getChildsByParentId = (childMajors,parentId) => {
        let childs = [];
        childMajors.forEach( child => {
            if (child.MajorParentID === parentId) {
                childs.push(child);
            }
        });
        return childs;
    }
    getDataTable = () => {
         let {
            parentMajors,
            childMajors
         }  = this.state;
         let data = [];
         parentMajors.forEach( (parentMajor,index) => {
             let tempChilds = this.getChildsByParentId(childMajors, parentMajor.MajorID);
             let childs = tempChilds.map( (child,index) => {
                return ({
                    key: child.MajorParentID+''+index,
                    childMajor: child.MajorName,
                    thm: 'A',
                    point: child.UniversityMajorPoint
                });
             });
             console.log(childs);
             data.push({
                 key: index,
                 parentMajor: parentMajor.MajorName,
                 children: childs
             });
         });
        this.setState({
            data: data
        });
    }
    componentDidMount() {
        let { id } = this.props.match.params;
        this.setState({
            universityId: id
        });
        let url = API.GET_UNIVERSITY;
        let body = {
            universityId: id
        }
        post(url, body).then(res => {
            let uniInfo = res.data.ResultData.UniGeneralInfos[0];
            let uniRangePoint = res.data.ResultData.UniRangePoint[0];
            let rangePoint = uniRangePoint.UniversityPointFrom +' ~ '+ uniRangePoint.UniversityPointTo;
            this.setState({
                universityName: uniInfo.UniversityName,
                universityShortDescription: uniInfo.UniversityShortDescription,
                uniRangePoint: rangePoint,
                universityJobAfterGraduating: uniInfo.UniversityJobAfterGraduating,
                universityYearsAVG: uniInfo.UniversityYearsAVG,
                universityLongDescription: uniInfo.UniversityLongDescription,
                universityFeeAVG: uniInfo.UniversityFeeAVG,
                parentMajors: res.data.ResultData.UniParentMajors,
                childMajors: res.data.ResultData.UniChildMajors,
            });
            this.getDataTable();
            console.log(res);
        }).catch(rej => {
            console.log(rej);
        });

    }
    render() {
         let {
            universityName,
            universityShortDescription,
            uniRangePoint,
            universityJobAfterGraduating,
            universityYearsAVG,
            universityLongDescription,
            universityFeeAVG,
            data
            } = this.state;
        let logo = require('../../assets/uni_logo/fpt.png');
        let mainImage = require('../../assets/university/fpt/1.jpg');
        let smallImage1 = require('../../assets/university/fpt/1.jpg');
        let smallImage2 = require('../../assets/university/fpt/2.jpg');
        let smallImage3 = require('../../assets/university/fpt/3.jpg');
        let smallImage4 = require('../../assets/university/fpt/4.jpg');
        return (
            <UniRPageLayout>
                <UniRContainerVertical className={classnames(cls.detail_content)}>
                    <UniRContainerHorizontal className={classnames(cls.introduction)}>
                        <UniRContainerVertical className={classnames(cls.logo_and_short_description)}>
                            <div className={classnames(cls.title)}>
                                {universityName}
                            </div>
                            <div className={classnames(cls.logo_wrapper)}>
                                {logo &&
                                    <Img  className={classnames(cls.logo)} src={logo}>
                                    </Img>
                                }
                            </div>
                            <div className={classnames(cls.short_description)}>
                                {universityShortDescription}
                            </div>
                        </UniRContainerVertical>
                        <div className={classnames(cls.image_lib)}>
                                <UniRImageGallery
                                />
                            {/* <div className={classnames(cls.main_image_wrapper)}>
                                {mainImage &&
                                    <Img className={classnames(cls.main_image)} src={mainImage}>
                                    </Img>
                                }
                            </div>
                            <div  className={classnames(cls.small_images_wrapper)}>
                                <Img  className={classnames(cls.small_image)} src={smallImage1}>
                                </Img>
                                <Img className={classnames(cls.small_image)} src={smallImage2}>
                                </Img>
                                <Img className={classnames(cls.small_image)} src={smallImage3}>
                                </Img>
                                <Img className={classnames(cls.small_image)} src={smallImage4}>
                                </Img>
                            </div> */}
                        </div>
                    </UniRContainerHorizontal>
                    <UniRContainerHorizontal className={classnames(cls.extra_infos)}>
                        <UniRContainerVertical className={classnames(cls.extra_infos_item)}>
                                <div className={classnames(cls.info_number)}>{uniRangePoint}</div>
                                <div className={classnames(cls.info_type)}>{infoType1}</div>
                        </UniRContainerVertical>
                        <UniRContainerVertical className={classnames(cls.extra_infos_item)}>
                                <div className={classnames(cls.info_number)}>{universityJobAfterGraduating}%</div>
                                <div className={classnames(cls.info_type)}>{infoType2}</div>
                        </UniRContainerVertical>
                        <UniRContainerVertical className={classnames(cls.extra_infos_item)}>
                                <div className={classnames(cls.info_number)}>{universityYearsAVG}</div>
                                <div className={classnames(cls.info_type)}>{infoType3}</div>
                        </UniRContainerVertical>
                    </UniRContainerHorizontal>
                    <div className={classnames(cls.long_description)}>
                    {universityLongDescription}
                    </div>
                    <div className={classnames(cls.free)}>
                    {universityFeeAVG}
                    </div>
                    <div className={classnames(cls.diemchuan)}>
                        <div className={classnames(cls.diemchuan_title)}>
                            {diemchuanTitle}
                        </div>
                        <div className={classnames(cls.diemchuan_table)}>
                        <Table columns={columns} dataSource={data} bordered />
                        </div>     
                    </div>
                </UniRContainerVertical>
            </UniRPageLayout>
        );
    }
}

export default UniDetail;