import React, { Component } from 'react';
import cls from './styles.module.scss';
import classnames from 'classnames';
import { UniRContainer } from '../../common';

type UniRWidgetProps = {
    children?: React.Node,
    className?: String,
    primary?: Boolean,
    shortDes?: Boolean,
    gray?: Boolean
}

class UniRWidget extends Component<UniRWidgetProps> {
    constructor(props) {
        super(props);
        this.state = {};
    }
    render() {
        let {
            children,
            primary,
            shortDes,
            gray,
            className,
            ...rest
        } = this.props;
        return (
            <UniRContainer
                className={classnames(
                    cls.widget_container,
                    gray && cls.gray,
                    shortDes && cls.short_des,
                    primary && cls.primary,
                    className
                )}
                {...rest}
            >
                {children}
            </UniRContainer>
        );
    }
}

UniRWidget.defaultProps = {
    children: null,
    className: null,
    primary: false,
    shortDes: false,
    gray: false
}

export default UniRWidget;