import { STATUS } from '../constant/status';
import types from '../actions/changePassword/types';

const initState = {
    userId: "",
    password: { data: "", error: "" },
    newPassword: { data: "", error: "" },
    rePassword: { data: "", error: "" },
    status: STATUS.default
}
const changePassword = (state = initState, action) => {
    switch (action.type) {
        case types.SET_STATUS:
        return {
            ...state,
            status: action.status,
            error: action.error
        }
        case types.SET_PASSWORD:
            return {
                ...state,
                password: action.password
            }
        case types.SET_NEW_PASSWORD:
        return {
            ...state,
            newPassword: action.newPassword
        }
        case types.SET_RE_PASSWORD:
        return {
            ...state, 
            rePassword: action.rePassword
        }
        default:
            return state;
    }
}
export default changePassword;