import { STATUS } from '../constant';
import types from '../actions/login/types';
import secureLs from 'secure-ls';
import { LS_KEY } from '../appConfig';
var ls = new secureLs();

const initState = {
    userFullName: "",
    userID: "",
    userEmail: { data: "", error: "" },
    userPassword: { data: "", error: "" },
    loginStatus: STATUS.default,
    loginError: "",
}

const login = (state = initState, action) => {

    switch (action.type) {
        case types.SET_LOG_IN_STATUS:
            return {
                ...state,
                loginStatus: action.status,
                loginError: action.error
            }
        case types.SET_USER:
            return {
                ...state,
                userID: action.userID,
                userEmail: action.userEmail,
                userFullName: action.userFullName,
            }
        case types.SET_STORAGE:
            setStorage(
                action.userID,
                action.userToken,
                action.userEmail,
                action.userFullName,
                action.isVerified
            );
            return {
                ...state
            }
        case types.SET_EMAIL:
            return {
                ...state,
                userEmail: action.userEmail
            }
        case types.SET_PASSWORD:
            return {
                ...state,
                userPassword: action.userPassword,
            }
        case types.CLEAR_LOG_IN:
            clearStorage();
            state = initState;
            return {
                ...state,
            }
        default:
            return state
    }
}

const setStorage = (userID, userToken, userEmail, userFullName, isVerified) => {
    console.log(userID, userToken, userEmail, userFullName, isVerified)
    let storage = ls.get(LS_KEY);
    if (!storage) {
        ls.set(
            LS_KEY,
            {
                userID,
                userToken,
                userEmail,
                userFullName,
                isVerified
            }
        )
    }
}

const clearStorage = () => {
    ls.remove(LS_KEY);
}

export default login