import React, { Component } from 'react';
import cls from './styles.module.scss';
import classnames from 'classnames';
import ReactLoading from 'react-loading';
import { UniRContainer } from '../../common';

type UniRLoadingProps = {
    className?: String,
    type?: String,
    color?: String,
    delay?: Number,
    height?: String | Number,
    width?: Number | String,
    containerStyle?: String,
    loadable?: Boolean
}

class UniRLoading extends Component<UniRLoadingProps> {
    constructor(props) {
        super(props);
        this.state = {};
    }
    render() {
        let {
            type,
            color,
            height,
            width,
            loadable,
            className,
            containerStyle
        } = this.props;
        return (
            <UniRContainer
                nomargin
                nopadding
                className={classnames(
                    cls.loading_container,
                    loadable && cls.loadable,
                    containerStyle
                )
                }>
                <ReactLoading
                    type={type}
                    color={color}
                    height={height}
                    width={width}
                    className={classnames(cls.loading_style, className)}
                />
            </UniRContainer>

        );
    }
}

UniRLoading.defaultProps = {
    className: null,
    containerStyle: null,
    type: "bubbles",
    color: "",
    height: "",
    width: "",
    loadable: false
}

export default UniRLoading;