import React, { PureComponent } from 'react';
import classnames from 'classnames';
import cls from './styles.module.scss';
import {
    Col,Icon
} from 'antd';
import Img from 'react-image';
import {
   UniRContainerVertical,
   UniRContainerHorizontal,
   UniRImage
} from '../../../common';
type BlogBoxProps = {
    className?: String,
    data?: Object,
}

class BlogBox extends PureComponent<BlogBoxProps> {
    constructor(props) {
        super(props);
        this.state = {};
    }

    handleUniClick = () => {

    }

    render() {
        let {
            className,data,
        } = this.props;
        data.shortDescription =  data.shortDescription.toString().length > 200 
                    ? data.shortDescription.toString().substring(0,200)+'...' 
                    : data.shortDescription.toString();
        return (
            <Col className={classnames(cls.divRoot, className)} span={12}>
                <UniRContainerHorizontal className={classnames(cls.blogBox)}>
                    <div className={classnames("col-5",cls.blogThumbContainer)}>
                      
                        <UniRImage 
                            className={classnames(cls.blogThumb) }
                            src={data.thumb} 
                            lightbox={true}
                        >
                        </UniRImage>

                    </div>
                    <UniRContainerVertical className={classnames("col-7")}>
                        <p className={cls.blogTitle}>
                            {data.title}
                        </p>
                        <p>
                            {data.shortDescription}
                        </p>
                    </UniRContainerVertical>
                </UniRContainerHorizontal>
                
            </Col>
        );
    }
}

BlogBox.defaultProps = {
    className: null,
    title: "Khoa ABC",
    link:"http://google.com/",
    icon:"close-circle"
}

export default BlogBox;