import React, { Component } from 'react';
import cls from './styles.module.scss';
import classnames from 'classnames';
import Img from 'react-image';
import { 
    UniRPageLayout,
    UniRContainerVertical,
    UniRContainerHorizontal,
    UniRContainer,
    UniRWidget
    } from '../../../../common';

class TopUniColumn extends Component {
    constructor(props) {
        super(props);
        this.state = {
            value: "",
        }
   }
   render() {
       let char_name = this.props.charName;
       let noBorderRight = this.props.noBorderRight;
       let fptLogo = require('../../../../assets/uni_logo/fpt.png');
        let rmitLogo = require('../../../../assets/uni_logo/rmit.png');
        let tluLogo = require('../../../../assets/uni_logo/tlu.jpg');
       return (
        <UniRContainer nomargin className={classnames(cls.top_uni_column,'col-4',noBorderRight && cls.no_border_right)}>
            <UniRContainerVertical>
                <div className={classnames(cls.top_uni_title)} nomargin>{char_name}</div>
                <div className={classnames(cls.top_uni_detail_item)}>
                    <UniRContainerHorizontal>
                        <div className={classnames(cls.top_uni_logo_wrapper)}>
                            <Img className={classnames(cls.top_uni_logo)} src={fptLogo}></Img>
                        </div>
                        <UniRContainerVertical className={classnames(cls.top_uni_info)}>
                            <div className={classnames(cls.top_uni_info_name)}>Trng i hc FPT</div>
                            <div className={classnames(cls.top_uni_info_mark)}>Khong im: 21</div>
                        </UniRContainerVertical>
                    </UniRContainerHorizontal>
                </div >
                <div className={classnames(cls.top_uni_detail_item)}>
                    <UniRContainerHorizontal>
                        <div className={classnames(cls.top_uni_logo_wrapper)}>
                            <Img className={classnames(cls.top_uni_logo)} src={rmitLogo}></Img>
                        </div>
                        <UniRContainerVertical className={classnames(cls.top_uni_info)}>
                            <div className={classnames(cls.top_uni_info_name)}>Trng i hc RMIT</div>
                            <div className={classnames(cls.top_uni_info_mark)}>Khong im: 21</div>
                        </UniRContainerVertical>
                    </UniRContainerHorizontal>
                </div >
                <div className={classnames(cls.top_uni_detail_item)}>
                    <UniRContainerHorizontal>
                        <div className={classnames(cls.top_uni_logo_wrapper)}>
                            <Img className={classnames(cls.top_uni_logo)} src={tluLogo}></Img>
                        </div>
                        <UniRContainerVertical className={classnames(cls.top_uni_info)}>
                            <div className={classnames(cls.top_uni_info_name)}>Trng i hc Thng Long</div>
                            <div className={classnames(cls.top_uni_info_mark)}>Khong im: 21</div>
                        </UniRContainerVertical>
                    </UniRContainerHorizontal>
                </div >
            
            </UniRContainerVertical>
        
        </UniRContainer> 
       );
   }
}
TopUniColumn.defaultProps = {
    charName: '',
    noBorderRight: false
};
export default TopUniColumn;