import React, { Component } from 'react';
import cls from './styles.module.scss';
import classnames from 'classnames';
import { UniRContainer, UniRButton } from '../../common';
import { Upload, Spin, Avatar, Icon } from 'antd';
import UniRContainerVertical from '../UniRContainerVertical';
import { connect } from 'react-redux';
import { API } from '../../constant';
import { BACKEND_URL_AKA } from '../../appConfig';

type UniRUploadImageProps = {
    accept?: String,
    title?: String,
    icon_upload?: React.node,
    handleChange?: Function,
    urldefault?: ImageBitmap,
    error?: String
}

const HTTP_ERROR = " c li xy ra!"
const AVATAR_SIZE = 250;
const SPIN_SIZE = `${AVATAR_SIZE / 5}px`;
const SPIN_ICON = <Icon
    type="loading"
    style={{
        fontSize: SPIN_SIZE,
        width: 'inherit',
        height: 'inherit'
    }}
    spin
/>;
const ICON_AVATAR_DEFAULT = "user";
const FILE_TYPE = [
    "image/jpeg",
    "image/jpg",
    "image/png",
    "image/gif"
];
const FILE_SIZE = 5;

class UniRUploadImage extends Component<UniRUploadImageProps> {
    constructor(props) {
        super(props);
        this.state = {
            fileList: [],
            response: {},
            uploading: false,
            uploadFile: {},
            httpError: "",
            api: ""
        };
    }

    beforeUpload = (file) => {
        let isValid = false;
        FILE_TYPE.map(f => {
            if (f === file.type) {
                isValid = true;
            }
        })
        if (!isValid) {
            this.setState({
                httpError: "File khng hp l (jpg, jpeg, png, gif)"
            })
        } else if (!isLt2M) {
            this.setState({
                httpError: "nh kch c qu ln (<= 5Mb)"
            })
        }
        let isLt2M = file.size / 1024 / 1024 <= FILE_SIZE;
        return isValid && isLt2M;
    }


    handleChange = (file) => {
        let { status, response } = file;
        this.setState({
            fileList: [file]
        })
        switch (status) {
            case 'uploading':
                this.setState({
                    uploading: true,
                    httpError: ""
                })
                break;
            case 'done':
                this.setState({
                    uploadFile: file,
                    uploading: false,
                    httpError: ""
                })
                this.props.handleChange(response);
                break;
            case 'error':
                this.setState({
                    uploadFile: {},
                    httpError: HTTP_ERROR
                })
            case 'remove': {
                this.setState({
                    uploadFile: {}
                })
            }
            default:
                this.setState({
                    uploading: false,
                    uploadFile: {}
                })
        }
    }

    render() {
        let { fileList, uploading, uploadFile, httpError } = this.state;
        let {
            accept,
            icon_upload,
            title,
            urldefault,
            error
        } = this.props;
        let { backendUrl } = this.props.menuBar;
        let api = API.UPLOAD_TEMP_IMAGE.replace(BACKEND_URL_AKA, backendUrl);
        let src = uploadFile.thumbUrl || uploadFile.url || urldefault;
        console.log(src)
        let avatarProps = { size: AVATAR_SIZE }
        src ? avatarProps.src = src : avatarProps.icon = ICON_AVATAR_DEFAULT;
        error = error || httpError;
        return (
            <UniRContainer nomargin className={classnames(cls.container)}>
                <UniRContainerVertical className={classnames(cls.avatar_upload_container)}>
                    <UniRContainer nopadding className={classnames(cls.avatar_container)}>
                        < div className={classnames(cls.avatar_not_loading, uploading && cls.avatar_loading)}>
                            <Spin indicator={SPIN_ICON} />
                        </div>
                        <Avatar  {...avatarProps} />
                    </UniRContainer>
                    <Upload
                        accept={accept}
                        className={classnames(cls.upload_container)}
                        listType='picture'
                        fileList={fileList}
                        action={api}
                        onChange={(file) => this.handleChange(file.file)}
                        beforeUpload={this.beforeUpload}
                    >
                        <UniRButton
                            disabled={uploading}
                            error={error}
                            block
                            type="primary"
                            btnStyle={classnames(cls.btn_title)}
                        >
                            {icon_upload} {title}
                        </UniRButton>
                    </Upload>

                </UniRContainerVertical>
            </UniRContainer>
        );
    }
}

UniRUploadImage.defaultProps = {
    accept: "image/*",
    title: "Upload",
    icon_upload: null,
    urldefault: null,
    error: "",
    handleChange: () => { }
}

const mapStateToProps = state => {
    return {
        menuBar: state.menuBar
    }
}

export default connect(
    mapStateToProps
)(UniRUploadImage);