import types from '../actions/menuBar/types';
import { getStorage, setStorage } from '../helper/axiosHelper';
import { STATUS } from '../constant';

const initState = {
    activeMenu: {},
    checkVerifiedStatus: STATUS.default,
    backendUrl: "",
    isVerified: true
}

const menuBar = (state = initState, action) => {

    switch (action.type) {
        case types.SET_ACTIVE_MENU:
            setActiveMenu(action.menu);
            return {
                ...state,
                activeMenu: action.menu
            }
        case types.SET_BACKEND_URL:
            return {
                ...state,
                backendUrl: action.backendUrl
            }
        case types.SET_CHECK_VERIFIED_STATUS:
            return {
                ...state,
                checkVerifiedStatus: action.status
            }
        case types.SET_IS_VERIFIED:
            return {
                ...state,
                isVerified: action.isVerified
            }
        default:
            return state
    }
}

const setActiveMenu = (menu) => {
    let storage = getStorage();
    if (!storage) {
        storage = { menu };
    } else {
        storage.menu = menu;
    }
    setStorage({ ...storage });
}

export default menuBar