import React, { Component } from 'react';
import cls from './styles.module.scss';
import classnames from 'classnames';
import { UniRContainer, UniRContainerVertical } from '../../common';
import Group from './Group';
import Dropdown from './Dropdown';

type UniRDropdownProps = {
    children?: React.Node,
    containerStyle?: String,
    label?: String,
    error?: String,
    errorStyle?: String,
    labelStyle?: String,
    data?: Array,
    dataid?: String,
    datavalue?: String,
    value?: String,
    onClick?: Function,
    loading?: Boolean,
    custom?: Boolean
}

class UniRDropdown extends Component<UniRDropdownProps> {
    constructor(props) {
        super(props);
        this.state = {};
    }

    static Group = Group;
    static Item = Dropdown;

    onClick = (value) => {
        this.props.onClick(value);
    }

    render() {
        let {
            children,
            containerStyle,
            label,
            error,
            errorStyle,
            labelStyle,
            data,
            dataid,
            datavalue,
            value,
            loading,
            custom
        } = this.props;

        return (
            <UniRContainer nomargin className={classnames(cls.dropdown_container, containerStyle)}>
                <UniRContainerVertical >
                    {label &&
                        <div className={classnames(cls.label, labelStyle)}>
                            {label}
                        </div>
                    }
                    {(children && custom) ?
                        <Dropdown
                            data={data}
                            dataid={dataid}
                            datavalue={datavalue}
                            value={value}
                            error={error}
                            onClick={(value) => this.onClick(value)}
                            loading={loading}
                        >
                            {children}
                        </Dropdown>
                        : children ||
                        <Dropdown
                            data={data}
                            dataid={dataid}
                            datavalue={datavalue}
                            value={value}
                            error={error}
                            onClick={(value) => this.onClick(value)}
                            loading={loading}
                            children={children}
                        />

                    }
                    {
                        children &&
                        <div className={classnames("unir_error", error && "unir_error_active", errorStyle)}>
                            {error}
                        </div>
                    }
                </UniRContainerVertical>
            </UniRContainer>
        );
    }
}

UniRDropdown.defaultProps = {
    children: null,
    containerStyle: null,
    label: null,
    error: null,
    errorStyle: null,
    labelStyle: null,
    data: [],
    dataid: "",
    datavalue: "",
    value: "",
    onClick: () => { },
    loading: false,
    custom: false
}

export default UniRDropdown;