    import React, { Component } from 'react';
    import cls from './styles.module.scss';
    import classnames from 'classnames';
    import Img from 'react-image';
    import TopUniColumn  from './TopUniColumn';
    import { 
        UniRPageLayout,
        UniRContainerVertical,
        UniRContainerHorizontal,
        UniRContainer,
        UniRWidget
     } from '../../../common';
     import {
         Icon
     } from 'antd';
     class TestResult extends Component{
        constructor(props) {
             super(props);
             this.state = {
                 value: "",
             }
        }
        
        render() {
            let characteristic = this.props.value;
            let fptLogo = require('../../../assets/uni_logo/fpt.png');
            let rmitLogo = require('../../../assets/uni_logo/rmit.png');
            let tluLogo = require('../../../assets/uni_logo/tlu.jpg');
            return (
                
                    <UniRContainerVertical >
                        <UniRContainerVertical className={classnames(cls.test_result_characteristic)}>
                            <div className={classnames(cls.characteristic_title)}>
                            Kt qu trc nghim cho thy, m tnh cch ca bn l
                            </div>
                            <UniRWidget primary className={classnames(cls.characteristic)} >
                            {characteristic}
                            </UniRWidget>
                        </UniRContainerVertical>
                        <UniRContainerVertical>
                            <UniRContainerHorizontal className={classnames(cls.char_detail)}>
                                <div className={classnames(cls.char_description_letter)}>
                                A
                                </div>
                                <div className={classnames(cls.char_description)}>
                                : C kh nng v ngh thut, kh nng v trc gic, kh nng tng tng cao, thch lm vic trong cc mi trng 
                                    mang tnh ngu hng, khng khun mu.
                                </div>
                            </UniRContainerHorizontal>
                            <UniRContainerHorizontal className={classnames(cls.career_recomendation)}>
                                <div className={classnames(cls.enter_icon)}>
                                    <Icon  type='enter'/>
                                </div>
                                <div className={classnames(cls.career_recomendation_description)}>
                                Ngnh ngh ph hp vi nhm ny bao gm: Cc ngnh v vn chng; bo ch (bnh lun vin, dn chng 
                                trnh...); in nh, sn khu, m thut, ca nhc, ma, kin trc, thi trang, hi ha, gio vin dy s/Anh vn, bo 
                                tng, bo tn...
                                </div>
                            </UniRContainerHorizontal>
                        </UniRContainerVertical>
                        <UniRContainerVertical>
                            <UniRContainerHorizontal className={classnames(cls.char_detail)}>
                                <div className={classnames(cls.char_description_letter)}>
                                I
                                </div>
                                <div className={classnames(cls.char_description)}>
                                : C kh nng v quan st, khm ph, phn tch nh gi v gii quyt cc vn .
                                </div>
                            </UniRContainerHorizontal>
                            <UniRContainerHorizontal className={classnames(cls.career_recomendation)}>
                                <div className={classnames(cls.enter_icon)}>
                                    <Icon  type='enter'/>
                                </div>
                                <div className={classnames(cls.career_recomendation_description)}>
                                Ngnh ngh ph hp vi nhm ny bao gm: Cc ngnh thuc lnh vc khoa hc t nhin (ton, l, ha, sinh, a 
                                l, a cht, thng k...); khoa hc x hi (nhn hc, tm l, a l...); y - dc (bc s gy m, hi sc, bc s phu 
                                thut, nha s...); khoa hc cng ngh (cng ngh thng tin, mi trng, in, vt l k thut, xy dng...); 
                                nng lm (nng hc, th y...).
                                </div>
                            </UniRContainerHorizontal>
                        </UniRContainerVertical>
                        <UniRContainerVertical>
                            <UniRContainerHorizontal className={classnames(cls.char_detail)}>
                                <div className={classnames(cls.char_description_letter)}>
                                R
                                </div>
                                <div className={classnames(cls.char_description)}>
                                : Ngi thuc nhm s thch ngh nghip ny thng c kh nng v k thut, cng ngh, h thng; a thch lm 
                                    vic vi  vt, my mc, ng thc vt; thch lm cc cng vic ngoi tri.
                                </div>
                            </UniRContainerHorizontal>
                            <UniRContainerHorizontal className={classnames(cls.career_recomendation)}>
                                <div className={classnames(cls.enter_icon)}>
                                    <Icon  type='enter'/>
                                </div>
                                <div className={classnames(cls.career_recomendation_description)}>
                                Ngnh ngh ph hp vi nhm ny bao gm nhng ngh v kin trc, an ton lao ng, ngh mc, xy dng, 
                                thy sn, k thut, my tu thy, li xe, hun luyn vin, nng - lm nghip (qun l trang tri, nhn ging c, 
                                lm nghip...), c kh (ch to my, bo tr v sa cha thit b, luyn kim, c kh ng dng, t ng...), in - in 
                                t, a l - a cht (o c, v bn  a chnh), du kh, hi dng hc, qun l cng nghip...
                                </div>
                            </UniRContainerHorizontal>
                        </UniRContainerVertical>
                        <UniRContainerHorizontal className={classnames(cls.top_uni)}>
                            <TopUniColumn charName='Artistic - Ngh thut' ></TopUniColumn>
                            <TopUniColumn charName='Artistic - Ngh thut' ></TopUniColumn>
                            <TopUniColumn charName='Artistic - Ngh thut' noBorderRight></TopUniColumn>
                        </UniRContainerHorizontal>
                    </UniRContainerVertical>
            );
        }
     }

export default TestResult;