import "@babel/polyfill";
import React from 'react';
import ReactDOM from 'react-dom';
import cls from './appConfig/app.module.scss';
import "antd/dist/antd.css";
import * as serviceWorker from './serviceWorker';
import configureStore from './configureStore';
import { Provider } from 'react-redux';
import Loadable from 'react-loadable';
import {
    BrowserRouter,
    Switch,
    Route,
    Redirect
} from 'react-router-dom';
import { PATH } from './constant';
import { UniRBackToTop, UniRLoading } from './common';
import {
    NoMatch,
    ErrorPage,
} from './containers';
import { getStorage } from './helper/axiosHelper';

const store = configureStore();
export default store;

const PrivateRoute = ({ component: Component, ...rest, }) => {
    let storage = getStorage();
    console.log(storage)
    let isAuthen = false;
    if (storage) {
        isAuthen = storage.userToken !== null;
    }
    let path = PATH.LOG_IN;
    return (
        <Route {...rest} render={(props) => (
            isAuthen ?
                <Component {...props} />

                : <Redirect to={{
                    pathname: path,
                    state: { from: props.location, isAuthen },
                }} />
        )} />
    )
}

const Loading = (props) => {
    if (props.error) {
        return <div>Error! <button onClick={props.retry}>Retry</button></div>;
    } else if (props.pastDelay) {
        return <UniRLoading loadable type="spinningBubbles"/>;
    } else {
        return <UniRLoading loadable type="spinningBubbles"/>;
    }
}

const PageContainer = Loadable({
    loader: () => import('./common/UniRPageContainer'),
    loading: Loading,
    pastDelay: 5000
})

const Home = Loadable({
    loader: () => import('./containers/Home'),
    loading: Loading,
    pastDelay: 5000

});

const Login = Loadable({
    loader: () => import('./containers/Login'),
    loading: Loading,
    pastDelay: 5000

});

const Profile = Loadable({
    loader: () => import('./containers/Profile'),
    loading: Loading,
    pastDelay: 5000

});

const UniSearch = Loadable({
    loader: () => import('./containers/UniSearch'),
    loading: Loading,
    pastDelay: 5000

});

const UniDetail = Loadable({
    loader: () => import('./containers/UniDetail'),
    loading: Loading,
    pastDelay: 5000

});

const Signup = Loadable({
    loader: () => import('./containers/Signup'),
    loading: Loading,
    pastDelay: 5000

});

const Blog = Loadable({
    loader: () => import('./containers/Blog'),
    loading: Loading,
    pastDelay: 5000

});

const BlogDetail = Loadable({
    loader: () => import('./containers/BlogDetail'),
    loading: Loading,
    pastDelay: 5000

});

const Router = () => {
    return (
        <Switch>
            <Route exact path={PATH.HOME} component={Home} />
            <Route exact path={PATH.LOG_IN} component={Login} />
            <Route exact path={PATH.SIGN_UP} component={Signup} />
            <PrivateRoute exact path={PATH.PROFILE} component={Profile} />
            <Route exact path={PATH.UNI_SEARCH} component={UniSearch} />
            <Route exact path={`${PATH.UNI_SEARCH}/:id`} component={UniDetail} />
            <Route exact path={PATH.UNI_BLOG} component={Blog} />
            <Route exact path={`${PATH.UNI_BLOG}/:id`} component={BlogDetail} />
            <Route exact path={PATH.ERROR_PAGE} component={ErrorPage} />
            <Route component={NoMatch} />
        </Switch>
    )
}


const Root = ({ store }) => (
    <PageContainer>
        <UniRBackToTop />
        <Provider store={store}>
            <BrowserRouter
            // basename={PATH.BASE_NAME}
            >
                <Router />
            </BrowserRouter>
        </Provider>
    </PageContainer>

)


ReactDOM.render(
    <Root store={store} />,
    document.getElementById('root')
);

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: http://bit.ly/CRA-PWA
serviceWorker.unregister();
