import React, { PureComponent } from 'react';
import classnames from 'classnames';
import cls from './styles.module.scss';
import {
    UniRWidthOverflow,
    UniRButton
} from '../../../common';

import Feedback from "../../../containers/Feedback";

type UniRFooterProps = {
    className?: String
}

class UniRFooter extends PureComponent<UniRFooterProps> {
    constructor(props) {
        super(props);
        this.state = {
            feedbackVisible : false
        };
    }

    onCancel = ()=>{
        this.setState({feedbackVisible:false});
    }
    
    showFeedback = ()=>{
        this.setState({feedbackVisible : true});
    }

    render() {
        let { className } = this.props;
        let feedbackVisible = this.state.feedbackVisible;
        return (
            <UniRWidthOverflow>
                <Feedback
                    visible={feedbackVisible}
                    onCancel={this.onCancel}
                ></Feedback>
                <div className={classnames(cls.footer, className)}>
                    <div className={classnames(cls.copyright)}>
                        Bn quyn thuc v Chondaihoc.com (University Recommendation)
                    </div>
                    <div className={classnames(cls.fbContainer)}>
                        <UniRButton
                            tag
                            btnStyle={classnames(cls.fbButton)}
                            title="Gi nh gi v phn hi"
                            onClick = {this.showFeedback}
                        />
                    </div>

                </div>
            </UniRWidthOverflow>
        );
    }
}

UniRFooter.defaultProps = {
    className: null
}

export default UniRFooter;