import React, { Component } from 'react';
import classnames from 'classnames';
import cls from './styles.module.scss';
import UniRHeader from './UniRHeader';
import UniRFooter from './UniRFooter';
import UniRBody from './UniRBody';
import { UniRContainerVertical } from '../../common';

type UniRPageLayoutProps = {
    className?: String,
    children?: React.Node,
    imagesSlideShow?: Array
}

class UniRPageLayout extends Component<UniRPageLayoutProps> {
    constructor(props) {
        super(props);
        this.state = {};
    }
    render() {
        let {
            children,
            className,
            imagesSlideShow
        } = this.props;
        return (
            <UniRContainerVertical className={classnames(className)}>
                <UniRHeader imgSlideShow={imagesSlideShow} />
                <UniRBody>
                    {children}
                </UniRBody>
                <UniRFooter />
            </UniRContainerVertical>
        );
    }
}

UniRPageLayout.defaultProps = {
    children: null,
    className: null,
    imagesSlideShow: []
}

export default UniRPageLayout;