import React, { PureComponent } from 'react';
import classnames from 'classnames';
import cls from './styles.module.scss';
import Img from 'react-image';
import { UniRButton, UniRContainerHorizontal } from '../../../../common';
import { connect } from 'react-redux';
import { withRouter } from 'react-router-dom';
import {
    setActiveMenu,
    setBackendUrl,
    checkVerified
} from '../../../../actions/menuBar';
import { PATH } from '../../../../constant';
import { getConfig, getStorage, setStorage } from '../../../../helper/axiosHelper';
import swal from 'sweetalert';

type MenuBarProps = {
    logo?: React.ElementType,
    className?: String,
    transparent?: Boolean
}

const menuList = [
    { id: 1, type: 0, title: "Gii thiu", path: "#" },
    { id: 2, type: 1, title: "Lm trc nghim", path: "#" },
    { id: 3, type: 0, title: "Tra cu i hc", path: PATH.UNI_SEARCH },
    { id: 4, type: 0, title: "Blog", path: PATH.UNI_BLOG },
]

class MenuBar extends PureComponent<MenuBarProps> {
    constructor(props) {
        super(props);
        this.state = {
            activeMenu: {}
        };
    }

    componentDidMount() {
        getConfig().then(res => {
            this.props.setBackendUrl(res.data.BACKEND_URL);
        })
        let path = this.props.location.pathname;
        menuList.map(menu => {
            if (path.includes(menu.path)) {
                this.setState({ activeMenu: menu })
            }
        })
        let storage = getStorage();
        if (storage) {
            let { isVerified, userID } = storage;
            if (window.location.pathname === PATH.HOME && !isVerified) {
                this.props.checkVerified(userID, (isVerified) => {
                    storage.isVerified = isVerified;
                    setStorage(storage);
                    isVerified && swal({
                        title: "Xc thc thnh cng!",
                        button: "OK!",
                        icon: "success",
                        className: "swal-btn",
                        text: "Ti khon ca bn  c xc thc thnh cng"
                    });
                },
                    (error) => {
                        swal({
                            title: "Li!",
                            button: "OK!",
                            icon: "error",
                            className: "swal-btn",
                            text: "Li kim tra xc thc"
                        });
                    }
                )
            }
        }

    }

    onLogoClick = () => {
        this.props.history.push(PATH.HOME);
    }

    changeDirect = (menu) => {
        this.props.history.push(menu.path);
    }

    render() {
        let {
            logo,
            className,
            transparent
        } = this.props;
        let { activeMenu } = this.state;
        let menuItems = [];
        menuList.map((item, i) => {
            let style = classnames(cls.header_menu_item);
            if (item.type === 1) {
                style = classnames(cls.header_menu_item, cls.hot_menu);
            }
            if (activeMenu) {
                if (item.id === activeMenu.id) {
                    style = classnames(cls.header_menu_item, cls.actived);
                }
            }

            return (
                menuItems.push(
                    <UniRButton
                        key={i}
                        tag
                        btnStyle={style}
                        title={item.title}
                        onClick={() => item.path !== "#" && this.changeDirect(item)}
                    />
                )
            )
        })
        return (
            <div className={classnames(
                cls.header_info,
                transparent && cls.transparent,
                className)}
            >
                <div className={classnames(cls.header_bar)}>
                    <div
                        className={classnames(cls.header_logo_container)}
                        onClick={this.onLogoClick}
                    >
                        {logo && <Img
                            className={classnames(cls.header_logo)}
                            src={logo}
                        />}
                    </div>
                    <UniRContainerHorizontal className={classnames(cls.header_menu, "col-5")}>
                        {menuItems}
                    </UniRContainerHorizontal>
                </div>
            </div>
        );
    }
}

MenuBar.defaulrProps = {
    logo: null,
    className: null,
    transparent: null
}

const mapStateToProps = state => {
    return {
        menuBar: state.menuBar
    }
}

const mapDispatchToProps = dispatch => {
    return {
        setActiveMenu: (menu) => {
            dispatch(setActiveMenu(menu))
        },
        setBackendUrl: (backendUrl) => {
            dispatch(setBackendUrl(backendUrl))
        },
        checkVerified: (userID, callBackSucces, callBackError) => {
            dispatch(checkVerified(userID, callBackSucces, callBackError))
        }
    }
}

export default withRouter(connect(
    mapStateToProps,
    mapDispatchToProps
)(MenuBar));