import React, { PureComponent } from 'react';
import classnames from 'classnames';
import cls from './styles.module.scss';
import {
    Col,Icon
} from 'antd';
import {
   UniRContainerVertical
} from '../../../common';
type MajorBoxProps = {
    className?: String,
    title?: String,
    link?: String,
    icon?:String,
}

class MajorBox extends PureComponent<MajorBoxProps> {
    constructor(props) {
        super(props);
        this.state = {};
    }

    handleMajorClick = () => {
        alert(this.props.link);
    }

    render() {
        let {
            className,title,
            link,icon
        } = this.props;
        return (
                <Col className={classnames(cls.box, className)} span={6}>
                    <div className={classnames(cls.content,cls.box)} onClick={()=> this.handleMajorClick()}>
                        <UniRContainerVertical>
                            <span  className={classnames(cls.linkIcon, cls.link)}><Icon type={icon}  /></span>
                            
                            <span className={cls.link}>
                                {title}
                            </span>
                            </UniRContainerVertical>
                    </div>
                </Col>
        );
    }
}

MajorBox.defaultProps = {
    className: null,
    title: "Khoa ABC",
    link:"http://google.com/",
    icon:"close-circle"
}

export default MajorBox;