import React, { Component } from 'react';
import cls from './styles.module.scss';
import classnames from 'classnames';
import ImageGallery from 'react-image-gallery';
import { UniRPanel } from '../../common';
import "react-image-gallery/styles/css/image-gallery.css";

type UniRImageGalleryProps = {
    images?: Array,
    className?: String
}

class UniRImageGallery extends Component<UniRImageGalleryProps> {
    constructor(props) {
        super(props);
        this.state = {};
    }

    renderImgData = (images) => {
        let temp = [];
        if (images.length === 0) {
            temp.push({
                original: require("../../assets/error/error_image.png"),
                thumbnail: require("../../assets/error/error_image.png")
            })
        } else {
            images.map((img) => {
                temp.push(
                    {
                        original: img,
                        thumbnail: img
                    }
                )
            })
        }
        return temp;
    }
    render() {
        let { images, className } = this.props;
        images = this.renderImgData(images);
        return (
            <UniRPanel nomargin className={classnames(cls.gallery_container, className)}>
                <ImageGallery
                    items={images}
                    showBullets
                    autoPlay
                    showPlayButton={false}
                    slideDuration={500}
                />
            </UniRPanel>

        );
    }
}

UniRImageGallery.defaultProps = {
    className: null,
    images: []
}

export default UniRImageGallery;