import React, { PureComponent } from 'react';
import classnames from 'classnames';
import cls from './styles.module.scss';
import { UniRContainerHorizontal } from '../../../common';

type UniRFormRowProps = {
    children?: React.Node,
    className?: String
}

class UniRFormRow extends PureComponent<UniRFormRowProps> {
    constructor(props) {
        super(props);
        this.state = {};
    }
    render() {
        let { children, className } = this.props;
        return (
            <UniRContainerHorizontal className={classnames(cls.form_row_container, className)}>
                {children}
            </UniRContainerHorizontal>
        );
    }
}

UniRFormRow.defaultProps = {
    children: null,
    className: null
}

export default UniRFormRow;