import React, { Component } from 'react';
import cls from './styles.module.scss';
import classnames from 'classnames';


type UniRContainerProps = {
    children?: React.Node,
    className?: String,
    nomargin?: Boolean,
    nopadding?: Boolean,
    paddinghorrizontal?: Boolean,
    paddingvertical?: Boolean,
    marginhorrizontal?: Boolean,
    marginvertical?: Boolean
}

class UniRContainer extends Component<UniRContainerProps> {
    constructor(props) {
        super(props);
        this.state = {};
    }
    render() {
        let {
            className,
            children,
            nomargin,
            nopadding,
            paddinghorrizontal,
            paddingvertical,
            marginhorrizontal,
            marginvertical,
            ...rest
        } = this.props;
        return (
            <div
                className={classnames(
                    cls.div_container,
                    nopadding && cls.no_padding,
                    nomargin && cls.no_margin,
                    paddinghorrizontal && cls.padding_horrizontal,
                    paddingvertical && cls.padding_vertical,
                    marginhorrizontal && cls.margin_horrizontal,
                    marginvertical && cls.margin_vertical,
                    className
                )}
                {...rest}
            >
                {children}
            </div>
        );
    }
}

UniRContainer.defaultProps = {
    children: null,
    className: null,
    nomargin: false,
    nopadding: false,
    paddinghorrizontal: false,
    paddingvertical: false,
    marginhorrizontal: false,
    marginvertical: false
}

export default UniRContainer;