import React, { PureComponent } from 'react';
import cls from './styles.module.scss';
import classnames from 'classnames';
import { Tree as AntdTree } from 'antd';
import { connect } from 'react-redux';
import { setCheckedMajors } from '../../../actions/uniSearch';
const { TreeNode } = AntdTree;

type TreeProps = {
    data?: Array,
    treeRef?: Function
}

class Tree extends PureComponent<TreeProps> {
    constructor(props) {
        super(props);
        this.state = {
            expandedKeys: [],
            autoExpandParent: true,
            checkedKeys: [],
            selectedKeys: [],
        };
    }

    onExpand = (expandedKeys) => {
        console.log('onExpand', expandedKeys);
        // if not set autoExpandParent to false, if children expanded, parent can not collapse.
        // or, you can remove all expanded children keys.
        this.setState({
            expandedKeys,
            autoExpandParent: false,
        });
    }

    onCheck = (checkedKeys) => {
        console.log('onCheck', checkedKeys);
        this.setState({ checkedKeys });
        this.props.setCheckedMajors(checkedKeys);
    }

    onSelect = (selectedKeys, info) => {
        console.log('onSelect', info);
        this.setState({ selectedKeys });
    }

    renderTreeNodes = data => data.map((item) => {
        if (item.children) {
            return (
                <TreeNode title={item.title} key={item.key} dataRef={item}>
                    {this.renderTreeNodes(item.children)}
                </TreeNode>
            );
        }
        return <TreeNode {...item} />;
    })


    render() {
        let { data } = this.props;
        let { checkedMajors } = this.props.uniSearch;
        return (
            <AntdTree
                checkable
                onExpand={this.onExpand}
                expandedKeys={this.state.expandedKeys}
                autoExpandParent={this.state.autoExpandParent}
                onCheck={this.onCheck}
                checkedKeys={checkedMajors}
                onSelect={this.onSelect}
                selectedKeys={this.state.selectedKeys}
                selectable={false}
            >
                {this.renderTreeNodes(data)}
            </AntdTree>
        );
    }
}

Tree.defaultProps = {
    data: [],
    treeRef: () => { }
}

const mapStateToProps = state => {
    return {
        uniSearch: state.uniSearch
    }
}

const mapDispatchToProps = dispatch => {
    return {
        setCheckedMajors: (checkedMajors) => {
            dispatch(setCheckedMajors(checkedMajors))
        }
    }
}

export default connect(
    mapStateToProps,
    mapDispatchToProps
)(Tree);