import React, { Component } from 'react';
import Img from 'react-image';
import cls from './styles.module.scss';
import UniRow from './UniRow';
import {
    UniRContainerVertical,
    UniRPageLayout,
    UniRButton,UniRContainerHorizontal, UniRImage
} from '../../common';
import classnames from 'classnames';
import MajorBox from './MajorBox';
import HomeBox from './HomeBox';
import BloxBox from './BlogBox';
import {
    Row,Col
} from 'antd';

import { connect } from "react-redux";
import {
    searchAct,
}
    from "../../actions/blog";
import BlogBox from './BlogBox';

const ROW_PER_PAGE = 4;

const images = [
    require('../../assets/slideshow/1.jpg'),
    require('../../assets/slideshow/2.jpg'),
    require('../../assets/slideshow/3.jpg'),
];
const uniData = [
    {
        logo: require('../../assets/uni_logo/fpt.png'),
        title: "Trng i hc FPT",
        shortDescription: `Chnh thc thnh lp ngy 8/9/2006 theo Quyt nh ca Th tng Chnh ph,
        Trng i hc FPT tr thnh trng i hc u tin ca Vit Nam do mt doanh nghip ng
        ra thnh lp vi 100% vn u t t Tp on FPT.`,
        mark: 21
    },
    {
        logo: require('../../assets/uni_logo/rmit.png'),
        title: "Trng i hc RMIT",
        shortDescription: `Ti RMIT Vit Nam, bn s c ha mnh v tri nghim mt nn
        gio dc c thit k  dn li thnh cng, gip trang b nhng k nng v tri thc cho
         bn tin xa trn con ng s nghip  chn.
        Trng cung cp a dng cc dch v h tr sinh vin v h thng c s vt cht ng cp.
        Hy gia nhp cng ng nng ng, thn thin ti RMIT v tn hng hng lot nhng hot ng x hi, vn ha, th thao v gii tr
        y mu sc ty theo s thch ca bn.`,
        mark: 21
    },
    {
        logo: require('../../assets/uni_logo/tlu.jpg'),
        title: "Trng i hc Thng Long",
        shortDescription: `Trng i hc Thng Long l mt c s kinh doanh dch v gio dc ti Vit Nam,
        l c s gio dc bc i hc ngoi cng lp u tin trong
        chnh th Cng ha x hi ch ngha Vit Nam, c thnh lp nm 1988.`,
        mark: 21
    },
];

class Home extends Component {
    constructor(props) {
        super(props);
        this.state = {
            blogs : []
        }
    }

    componentDidMount(){
        this.props.searchAct("", 0, ROW_PER_PAGE, (data) => {
            let d = JSON.parse(data);

            let blogs = JSON.parse(d.data);
        
            this.setState({
                blogs: blogs
            });
        })
    }

    render() {
        let unis = [];
        uniData.map((uni, i) => (
            unis.push(
                <UniRow
                    key={i}
                    data={uni}
                />
            )
        ));

        let blogs= this.state.blogs;
        
        let blogData =[];

        blogs.map((blog, i) =>(

            
            blogData.push(<BlogBox key={i} data = {
                
                Object.assign({}, blog, {
                    thumb: blog.ArticleImages,
                    title: blog.ArticleTitle,
                    shortDescription: "",
                    link:'https://www.google.com/'
                })

            } />)));

       
        return (
            <UniRContainerVertical>
                <UniRPageLayout imagesSlideShow={images}>
                    <HomeBox title="Trc nghim chn i hc" position="center" className={classnames(cls.divClickToTest)}>
                            <div className={classnames(cls.body)}>
                                
                              <div>
                              <UniRImage 
                                src={require('../../assets/home/test.jpg')}
                                className={classnames(cls.avatar)}
                              />
                              </div>
                                
                                
                                <UniRButton size="large" type="primary" title={"Lm bi Test"} btnStyle={classnames(cls.button)}/> 
                            </div>
                    </HomeBox>
                    <HomeBox title="Khi ngnh" position="left" className={classnames(cls.major)}>
                        <Row gutter={16} className={classnames(cls.majorRow)}>
                            <MajorBox title="Cng ngh thng tin"
                                link="http://google.com.vn/" icon="code"
                            />
                            <MajorBox title="Kinh t"
                                link="http://google.com.vn/" icon="code"
                            />
                            <MajorBox title="Cng ngh thng tin"
                                link="http://google.com.vn/" icon="code"
                            />
                            <MajorBox title="Cng ngh thng tin"
                                link="http://google.com.vn/" icon="code"
                            />
                            <MajorBox title="Cng ngh thng tin"
                                link="http://google.com.vn/" icon="code"
                            />
                            <MajorBox title="Cng ngh thng tin"
                                link="http://google.com.vn/" icon="code"
                            />
                            <MajorBox title="Cng ngh thng tin"
                                link="http://google.com.vn/" icon="code"
                            />
                            <MajorBox title="Cng ngh thng tin"
                                link="http://google.com.vn/" icon="code"
                            />
                        </Row>
                    </HomeBox>
                    <HomeBox title="i hc ni bt" position="right" className={classnames(cls.university)}>
                        {unis}
                    </HomeBox>  
                    <HomeBox title="Blog" position="left" className={classnames(cls.blog)}>
                        <Row gutter={16} className={classnames(cls.blogRow)}>
                            {blogData}
                        </Row>
                    </HomeBox>  
                </UniRPageLayout>
            </UniRContainerVertical>
        )
    }
}

const mapStateToProps = state => {
    return {
        blog: state.blog
    }
}

const mapDispatchToProps = dispatch => {
    return {
        searchAct: (whereClause, indexPage, ROW_PER_PAGE, callbackSuccess) => {
            dispatch(searchAct(whereClause, indexPage, ROW_PER_PAGE, callbackSuccess))
        }
    }
}

export default connect(
    mapStateToProps,
    mapDispatchToProps
)(Home);


