import React, { Component } from 'react';
import cls from './styles.module.scss';
import TestResult from './TestResult';
import ChangePassword from './ChangePassword';
import { UniRPageLayout } from '../../common';
import classnames from 'classnames';
import { connect } from 'react-redux';
import { getStorage, post } from '../../helper/axiosHelper';
import { getProfile, setFullName } from '../../actions/profile';
import { API } from '../../constant';
import swal from 'sweetalert';
import {
    UniRButton,
    UniRContainer,
    UniRInput,
    UniRContainerHorizontal,
    UniRContainerVertical,
    UniRUploadImage,
    UniRDropdown
} from '../../common';
import {
    Tabs
}
    from 'antd';
import { BACKEND_URL_AKA } from '../../appConfig';
let infoTab = 'Thng tin c nhn';
let resultTestTab = 'Kt qu trc nghim';
let btnTitle1 = 'Thay i nh i din';
let btnTitle2 = 'Lu thay i';
let usernameLabel = 'Tn ngi dng';
let dobLabel = 'Ngy sinh';
let username = 'Admin';
const CHANGE_PASSWORD_TITLE = 'i mt khu';
const BIRTHDAY_MSG_ERROR = "Ngy sinh khng hp l";
const FAKE_PASSWORD = 'ABC';
const HOUR = 10;
const ENTITY_NAME = 'User';
const EDIT_MODE = 'Edit';
class Profile extends Component {
    constructor(props) {
        super(props);
        this.state = {
            day: '',
            month: '',
            year: '',
            userFullName: '',
            item_error1: '',
            item_error2: '',
            item_error3: '',
            group_error: '',
            email: '',
            visible: false
        }
    }

    componentDidMount() {
        let id = getStorage().userID;
        console.log(id);
        this.props.getProfile(id, (data) => {
            let { day, month, year, userFullName } = this.props.profile;
            this.setState({
                day,
                month,
                year,
                userFullName
            })
        });
    }

    onChange = (key, value, error = "") => {
        this.props[key](value, error);
    }
    onSubmit = () => {

        let {
            day,
            month,
            year,
            userFullName
        } = this.state;
        let {
            userAvatar,
            userEmail
        } = this.props.profile;

        let date = new Date(month + "/" + day + "/" + year);
        date.setHours(HOUR);

        if (date.getDate() == day) {
            this.setState({
                group_error: ''
            });
            let changeProfileUrl = API.CHANGE_PROFILE;
            let userId = getStorage().userID;
            this.onMoveAvatar(userAvatar, userId, () => {
                let { backendUrl } = this.props.menuBar;
                let avatar = API.AVATAR_URL.replace('{1}', userId).replace('{2}', userAvatar);
                avatar = avatar.replace(BACKEND_URL_AKA, backendUrl);
                let body = {
                    entityName: ENTITY_NAME,
                    masterData: JSON.stringify({
                        UserID: userId,
                        UserPassword: FAKE_PASSWORD,
                        UserFullName: userFullName,
                        UserDOB: date,
                        UserAvatar: avatar,
                        UserEmail: userEmail
                    }),
                    mode: EDIT_MODE
                }
                post(changeProfileUrl, body).then(res => {
                    swal({
                        title: "Thay i thng tin c nhn thnh cng",
                        button: "OK",
                        icon: "success",
                    });

                }).catch(err => {
                    swal({
                        title: "Thay i thng tin c nhn khng thnh cng",
                        button: "OK",
                        icon: "error",
                        className: "swal-but"
                    });
                });
            });

        } else {
            this.setState({
                group_error: BIRTHDAY_MSG_ERROR
            });

        }
    }
    onMoveAvatar = (fileName, userID, callBackSuccess) => {
        let moveAvatarUrl = API.MOVE_AVATAR;
        let body = {
            fileName,
            userID
        }
        post(moveAvatarUrl, body).then(res => {
            console.log(res);
            callBackSuccess();
        }).catch(err => {
            console.log(err);
        })
    }
    onChangeFullName = (value) => {
        this.setState({
            userFullName: value
        });
    }
    onClick = (value, type = "0") => {
        let state = this.state;
        switch (type) {
            case "day":
                this.setState({
                    day: value.value
                });
                break;
            case "month":
                this.setState({
                    month: value.value
                });
                break;
            case "year":
                this.setState({
                    year: value.value
                });
                break;
        }
    }
    handleCancel = () => {
        this.setState({
            visible: false
        })
    }
    handleImage = (response) => {
        console.log(response)
        this.props.profile.userAvatar = response.ResultData;
    }

    onClickChangePasswordBtn = () => {
        this.setState({
            visible: true
        })
    }
    render() {
        let days = [];

        for (var i = 1; i <= 31; i++) days.push(i);
        let months = [];
        for (var i = 1; i <= 12; i++) months.push(i);
        let years = [];
        for (var i = 2019; i >= 1900; i--) years.push(i);

        let visible = this.state.visible;
        console.log(this.props.profile);
        let {

            userAvatar,
            userEmail
        } = this.props.profile;
        let {
            day,
            month,
            year,
            userFullName
        } = this.state;

        return (

            <UniRPageLayout>
                <UniRContainer className={classnames(cls.bgprofile)}>
                    <Tabs defaultActiveKey="1">
                        <Tabs.TabPane tab={infoTab} key="1" >
                            <UniRContainerHorizontal className={classnames(cls.profiledetail)}>
                                <ChangePassword
                                    userEmail={userEmail}
                                    visible={visible}
                                    title={CHANGE_PASSWORD_TITLE}
                                    onCancel={() => this.handleCancel()}
                                />
                                <div className={classnames(cls.profile_avatar_frame)}>
                                    <UniRUploadImage className={classnames(cls.upload_image)}
                                        urldefault={userAvatar}
                                        title={btnTitle1}
                                        handleChange={(response) => this.handleImage(response)}
                                    />
                                </div>
                                <UniRContainerHorizontal className={cls.profile_fields_wrapper}>
                                    <UniRContainerVertical>
                                        <UniRContainerVertical className={classnames(cls.profile_fields)}>
                                            <UniRInput
                                                defaultValue={username}
                                                label={usernameLabel}
                                                value={userFullName}
                                                onChange={(value) => this.onChangeFullName(value)}
                                                labelStyle={classnames(cls.input_title)}
                                            />
                                            <UniRDropdown className={classnames(cls.dropdown_wrapper)}
                                                label={dobLabel}
                                                error={this.state.group_error}
                                                labelStyle={classnames(cls.input_title)}
                                            >
                                                <UniRDropdown.Group>
                                                    <UniRDropdown.Item
                                                        value={day}
                                                        data={days}
                                                        onClick={(value) => this.onClick(value, 'day')}
                                                        error={this.state.item_error1}
                                                    />
                                                    <UniRDropdown.Item
                                                        value={month}
                                                        data={months}
                                                        onClick={(value) => this.onClick(value, 'month')}
                                                        error={this.state.item_error2}

                                                    />
                                                    <UniRDropdown.Item
                                                        error={this.state.item_error3}
                                                        value={year}
                                                        data={years}
                                                        onClick={(value) => this.onClick(value, 'year')}
                                                    />
                                                </UniRDropdown.Group>
                                            </UniRDropdown>
                                            <div className={classnames(cls.change_password_btn_wrapper)}>
                                                <UniRButton title={CHANGE_PASSWORD_TITLE}
                                                    btnStyle={classnames(cls.change_password_btn)}
                                                    onClick={() => this.onClickChangePasswordBtn()}
                                                />
                                            </div>
                                        </UniRContainerVertical>
                                        <UniRContainerHorizontal className={classnames(cls.save_change_btn_div)}>
                                            <UniRContainerVertical className={cls.save_change_btn_wrapper}>
                                                <UniRButton title={btnTitle2}

                                                    onClick={() => this.onSubmit()}
                                                    type='primary'>
                                                </UniRButton>
                                            </UniRContainerVertical>

                                        </UniRContainerHorizontal>
                                    </UniRContainerVertical>

                                </UniRContainerHorizontal>
                            </UniRContainerHorizontal>
                        </Tabs.TabPane>
                        <Tabs.TabPane tab={resultTestTab} key='2'>
                            <TestResult value='AIR'>

                            </TestResult>
                        </Tabs.TabPane>
                    </Tabs>
                </UniRContainer>
            </UniRPageLayout>
        );
    }
}
const mapDispatchToProps = dispatch => {
    return {
        getProfile: (id, callBackSuccess) => {
            dispatch(getProfile(id, callBackSuccess));
        },
        setFullName: (data, error) => {
            dispatch(setFullName(data, error));
        }
    }
}
const mapStateToProps = state => {
    return {
        profile: state.profile,
        menuBar: state.menuBar
    }
}

export default connect(
    mapStateToProps,
    mapDispatchToProps
)(Profile);
