import React, { Component } from 'react';
import classnames from 'classnames';
import cls from './styles.module.scss';
import { setPassword, setNewPassword, setRePassword,changePasswordAct  } from '../../../actions/changePassword';
import { STATUS, PATH } from '../../../constant';
import { connect } from 'react-redux';
import swal from 'sweetalert';
import {
    UniRButton,
    UniRInput,
    UniRModal,
    UniRForm
} from '../../../common';
type ChangePasswordProps = {
    title?: String,
    visible?: Boolean,
    onCancel?: Function,
    userEmail?: String
}
const title = 'i mt khu';
const PASSWORD_TITLE = 'Mt khu hin ti';
const NEW_PASSWORD_TITLE = 'Mt khu mi';
const RE_PASSWORD_TITLE = 'Xc nhn mt khu mi';
const PASSWORD_LENGTH = 3;
const PASSWORD_LENGTH_VALID = " di mt khu t nht 3";
const PASSWORD_COINCIDE = "Mt khu khng khp";
class ChangePassword extends Component<ChangePasswordProps> {
    constructor(props) {
        super(props);
        this.state = {
            // responseFb: null,
        };
    }
    onChange = (key, value, error = "") => {
        console.log(key + ':' + value);
        this.props[key](value, error);
    }
    getResult = (chk, msg) => {
        return { result: chk, msg: msg };
    }
    //#region valid
    isPasswordValid = (pass) => {
        var chkWithoutSpace = (pass.replace(/ /g, '').length >= PASSWORD_LENGTH);

        if (!chkWithoutSpace) return this.getResult(false, PASSWORD_LENGTH_VALID);
        else return this.getResult(true, "");
    }

    isConfirmPasswordValid(confirmPass, pass) {
        var confirmPassWithoutSpace = confirmPass.replace(/ /g, '');
        var passWithoutSpace = pass.replace(/ /g, '');

        var chkCoincide = (passWithoutSpace == confirmPassWithoutSpace);

        if (chkCoincide === false) return this.getResult(false, PASSWORD_COINCIDE);
        else return this.getResult(true, "");
    }
    //#endregion valid
    handleChangePassword = () => {
        let {
            password,
            newPassword,
            rePassword
        } = this.props.changePassword;
        let userEmail = this.props.userEmail;
        let pw = password.data;
        let newPw = newPassword.data;
        let rePw = rePassword.data;
        let chkPass = this.isPasswordValid(newPw);
        let chkConfirmPassword = this.isConfirmPasswordValid(rePw, newPw);
        if (chkPass.result == false || chkConfirmPassword.result == false) {
            this.onChange("setNewPassword", newPw, chkPass.msg);
            this.onChange("setRePassword", rePw, chkConfirmPassword.msg);
        } else {
            this.props.changePasswordAct(pw, newPw, userEmail, (res) => {
                console.log('aaaaaaaaaaaa')
                if(res.data.Error == false) {
                    swal({
                        title: res.data.Message,
                        button: "OK",
                        icon: "success",
                    });
                } else {
                    this.onChange("setPassword", pw,res.data.Message);
                }
            });
        }
    }
    handleCancel = () => {
        this.props.onCancel();
    }
    render() {
        console.log(this.props);
        let {
            title,
            visible
        } = this.props;
        let {
            password,
            newPassword,
            rePassword,
            status
        } = this.props.changePassword;
        let isLoading = status === STATUS.loading;
        let changePasswordBtnDisabled = !password.data || !newPassword.data || !rePassword.data;
        let footer = <div></div>;
        return (
            <UniRModal visible={visible}
                onCancel={this.handleCancel}
                title={title}
                footer={footer}
            >
                <UniRForm
                    className={classnames(cls.form_container)}
                    onSubmit={this.handleSubmit}>

                    <UniRForm.Layout body>

                        <UniRForm.Row>
                            <UniRInput
                                password
                                onChange={(value) => this.onChange("setPassword", value)}
                                label={PASSWORD_TITLE}
                                value={password.data}
                                error={password.error}
                            />
                        </UniRForm.Row>

                        <UniRForm.Row>
                            <UniRInput
                                password
                                label={NEW_PASSWORD_TITLE}
                                value={newPassword.data}
                                error={newPassword.error}
                                onChange={(value) => this.onChange("setNewPassword", value)}
                            />
                        </UniRForm.Row>

                        <UniRForm.Row>
                            <UniRInput
                                password
                                label={RE_PASSWORD_TITLE}
                                value={rePassword.data}
                                error={rePassword.error}
                                onChange={(value) => this.onChange("setRePassword", value)}
                            />
                        </UniRForm.Row>

                    </UniRForm.Layout>
                    <UniRForm.Layout footer>
                        <UniRForm.Row className={classnames(cls.container_submit)}>
                            <UniRButton
                                containerStyle={cls.btn_submit}
                                type="primary"
                                loading={isLoading}
                                onClick={() => this.handleChangePassword()}
                                disabled={changePasswordBtnDisabled}
                            >
                                {title}
                              </UniRButton>

                        </UniRForm.Row>

                    </UniRForm.Layout>

                </UniRForm>
            </UniRModal>
        );

    }
}
ChangePassword.defaultProps = {
    title: "",
    visible: false,
    userEmail: "",
    onCancel: () => { }
}
const mapDispatchToProps = dispatch => {
    return {
        changePasswordAct: (password, newPassword, userEmail, callBackSuccess) => {
            dispatch(changePasswordAct(password, newPassword, userEmail,callBackSuccess));
        },
        setPassword: (data, error) => {
            dispatch(setPassword(data, error));
        },
        setNewPassword: (data, error) => {
            dispatch(setNewPassword(data, error));
        },
        setRePassword: (data, error) => {
            dispatch(setRePassword(data, error));
        }
    }
}
const mapStateToProps = state => {
    return {
        changePassword: state.changePassword
    }
}

export default connect(
    mapStateToProps,
    mapDispatchToProps
)(ChangePassword);
