import React, { Component } from 'react';
import classnames from 'classnames';
import cls from './styles.module.scss';
import { UniRContainerVertical, UniRLoading, UniRContainerHorizontal } from '../../common';
import { Button } from 'antd';

type UniRButtonProps = {
    containerStyle?: String,
    loading?: Boolean,
    btnStyle?: String,
    errStyle?: String,
    error?: String,
    title?: String,
    onClick?: Function,
    children?: React.node,
    tag?: Boolean,
    disabled?: Boolean
}

class UniRButton extends Component<UniRButtonProps> {
    constructor(props) {
        super(props);
        this.state = {};
    }

    render() {
        let {
            containerStyle,
            loading,
            btnStyle,
            errStyle,
            error,
            title,
            children,
            tag,
            ...rest
        } = this.props;
        return (
            <UniRContainerVertical className={classnames(cls.container, containerStyle)}>
                <Button
                    className={classnames(tag && cls.tagStyle,cls.content, btnStyle)}
                    onClick={() => this.props.onClick()}
                    loading={loading}
                    {...rest}
                >
                    <UniRContainerHorizontal className={classnames(cls.content)}>
                        {children}
                        <span title={title} className={classnames(cls.title)}>
                            {title}
                        </span>

                    </UniRContainerHorizontal>

                </Button>
                <div className={classnames("unir_error", error && "unir_error_active", errStyle)}>
                    {error}
                </div>
            </UniRContainerVertical>
        );
    }
}

UniRButton.defaultProps = {
    containerStyle: null,
    loading: false,
    btnStyle: null,
    errStyle: null,
    error: null,
    title: "",
    onClick: () => { },
    children: null,
    tag: false,
    disabled: false
}

export default UniRButton;