import React, { Component } from 'react';
import classnames from 'classnames';
import cls from './styles.module.scss';

type UniRContainerVerticalProps = {
    className?: String,
    children?: React.node;
    Component?: React.ElementType;
}

class UniRContainerVertical extends Component<UniRContainerVerticalProps> {
    constructor(props) {
        super(props);
        this.state = {};
    }
    render() {
        let {
            className,
            children,
            Component,
            ...rest
        } = this.props;
        return (
            <Component
                className={classnames(cls.v_container, className)}
                {...rest}
            >
                {children}
            </Component>
        );
    }
}

UniRContainerVertical.defaultProps = {
    className: null,
    children: null,
    Component: 'div',
}

export default UniRContainerVertical;