import React, { Component } from 'react';
import cls from './styles.module.scss';
import UniRow from './UniRow';
import classnames from 'classnames';
import {
    UniRContainerVertical,
    UniRPageLayout,
    UniRButton,
    UniRContainerHorizontal,
    UniRWidget,
    UniRPanel
} from '../../common';
import {
    Input,
    Dropdown,
    Button,
    Menu,
    Icon
} from 'antd';

const images = [
    require('../../assets/slideshow/1.jpg'),
    require('../../assets/slideshow/2.jpg'),
    require('../../assets/slideshow/3.jpg'),
];

const uniData = [
    {
        logo: require('../../assets/uni_logo/fpt.png'),
        title: "Trng i hc FPT",
        shortDescription: `Chnh thc thnh lp ngy 8/9/2006 theo Quyt nh ca Th tng Chnh ph,
        Trng i hc FPT tr thnh trng i hc u tin ca Vit Nam do mt doanh nghip ng
        ra thnh lp vi 100% vn u t t Tp on FPT.`,
        mark: 21
    },
    {
        logo: require('../../assets/uni_logo/rmit.png'),
        title: "Trng i hc RMIT",
        shortDescription: `Ti RMIT Vit Nam, bn s c ha mnh v tri nghim mt nn
        gio dc c thit k  dn li thnh cng, gip trang b nhng k nng v tri thc cho
         bn tin xa trn con ng s nghip  chn.
        Trng cung cp a dng cc dch v h tr sinh vin v h thng c s vt cht ng cp.
        Hy gia nhp cng ng nng ng, thn thin ti RMIT v tn hng hng lot nhng hot ng x hi, vn ha, th thao v gii tr
        y mu sc ty theo s thch ca bn.`,
        mark: 21
    },
    {
        logo: require('../../assets/uni_logo/tlu.jpg'),
        title: "Trng i hc Thng Long",
        shortDescription: `Trng i hc Thng Long l mt c s kinh doanh dch v gio dc ti Vit Nam,
        l c s gio dc bc i hc ngoi cng lp u tin trong
        chnh th Cng ha x hi ch ngha Vit Nam, c thnh lp nm 1988.`,
        mark: 21
    },
]

class Search extends Component {
    constructor(props) {
        super(props);
        this.state = {
            selected: null
        };
    }

    handleClick = (e) => {
        this.setState({ selected: { key: e.key, value: e.item.props.children } })
    }

    render() {
        const menu = (
            <Menu onClick={this.handleClick}>
                <Menu.Item key="0">Tt c</Menu.Item>
                <Menu.Item key="1">Cng ngh thng tin</Menu.Item>
                <Menu.Item key="2">Thit k  ha</Menu.Item>
                <Menu.Item key="3">Kinh doanh quc t</Menu.Item>
            </Menu >
        );

        let dropdownPlacholder =
            this.state.selected !== null ?
                this.state.selected.value :
                "Tt c";

        let unis = [];
        uniData.map((uni, i) => (
            unis.push(
                <UniRow
                    key={i}
                    data={uni}
                />
            )
        ))
        return (
            <UniRContainerVertical>
                <UniRPageLayout imagesSlideShow={images}>
                    <UniRContainerHorizontal>
                        <div className={classnames("col-7")}>
                            <Input
                                allowClear
                                placeholder="Nhp tn trng i hc"
                                size="large"
                            />
                        </div>
                        <div className={classnames(cls.dropdown, "col-4")}>
                            <Dropdown overlay={menu}>
                                <Button size="large">
                                    {dropdownPlacholder}
                                    <span>
                                        <Icon type="down" />
                                    </span>
                                </Button>
                            </Dropdown>
                        </div>
                        <div className={classnames("col-1")}>
                            <UniRButton
                                size="large"
                                icon="search"
                                loading={false}
                                onClick={() => { }}
                            />
                        </div>
                    </UniRContainerHorizontal>
                    <div className={cls.res_count}>
                        3 Results
                        </div>
                    <UniRPanel>
                        <UniRPanel.Header title="Test Header" />
                        <UniRPanel.Body>
                            {unis}
                        </UniRPanel.Body>
                    </UniRPanel>
                </UniRPageLayout>
            </UniRContainerVertical>
        );
    }
}

export default Search;