import React, { Component } from 'react';
import classnames from 'classnames';
import cls from './styles.module.scss';
import Img from 'react-image';
import { connect } from 'react-redux';
import {
    loginAct,
    setEmail,
    setPassword
} from '../../actions/login';
import {
    UniRContainerHorizontal,
    UniRPageLayout,
    UniRForm,
    UniRInput,
    UniRButton,
    UniRImageGallery
} from '../../common';
import FacebookLogin from 'react-facebook-login';
import { FACEBOOK_APP_ID } from '../../appConfig';
import { STATUS, PATH } from '../../constant';

const FACEBOOK_GET_FIELDS = "name,email,picture";
const USER_PLACEHOLDER = "Nhp tn ngi dng";
const PASSWORD_PLACEHOLDER = "Nhp mt khu";

class Login extends Component {
    constructor(props) {
        super(props);
        this.state = {
            responseFb: null,
        };
    }

    handleSubmit = () => {
        let { userEmail, userPassword } = this.props.login;

        this.props.loginAct(userEmail, userPassword, () => {
            this.props.history.push(PATH.HOME);
        })
    }

    onChange = (key, value) => {
        this.props[key](value, "");
    }

    responseFacebook = (response) => {
        this.setState({
            responseFb: {
                name: response.name,
                email: response.email,
                picture: response.picture.data.url
            }
        })
    }

    handleCancel = () => {
        this.setState({
            visible: false
        })
    }


    render() {
        let {
            responseFb
        } = this.state;
        let {
            userEmail,
            userPassword,
            loginStatus
        } = this.props.login;
        let isLoading = loginStatus === STATUS.loading;
        let disabled = !userEmail.data || !userPassword.data;
        let infor = responseFb ?
            <UniRContainerHorizontal>
                <div className={classnames("col-6")}>
                    <div className={classnames("col-4")}>
                        <Img src={responseFb.picture} />
                    </div>
                    <div className={classnames("col-8")}>
                        <p>
                            Name: {responseFb.name}
                        </p>
                        <p>
                            Email: {responseFb.email}
                        </p>
                    </div>

                </div>
            </UniRContainerHorizontal> :
            null;
        return (
            <UniRPageLayout>
                <UniRForm
                    className={classnames(cls.form_container)}
                    onSubmit={this.handleSubmit}
                >
                    <UniRForm.Layout header title="ng nhp" />

                    <UniRForm.Layout body>

                        <UniRForm.Row>
                            <UniRInput
                                type="email"
                                onChange={(value) => this.onChange("setEmail", value)}
                                label="Email"
                                value={userEmail.data}
                                error={userEmail.error}
                            />
                        </UniRForm.Row>

                        <UniRForm.Row>
                            <UniRInput
                                password
                                label="Mt khu"
                                value={userPassword.data}
                                error={userPassword.error}
                                onChange={(value) => this.onChange("setPassword", value)}
                            />
                        </UniRForm.Row>

                    </UniRForm.Layout>
                    <UniRForm.Layout footer>
                        <UniRForm.Row className={classnames(cls.container_submit)}>
                            <UniRButton
                                containerStyle={cls.btn_submit}
                                type="primary"
                                htmlType="submit"
                                loading={isLoading}
                                disabled={disabled}
                                title={"ng nhp"}
                            />
                            <a className="login-form-forgot" href=""> Qun mt khu </a>
                        </UniRForm.Row>

                    </UniRForm.Layout>

                </UniRForm>

                {infor}
            </UniRPageLayout >
        );
    }
}

const mapDispatchToProps = dispatch => {
    return {
        loginAct: (userEmail, userPassword, callBackSuccess) => {
            dispatch(loginAct(userEmail, userPassword, callBackSuccess))
        },
        setEmail: (data, error) => {
            dispatch(setEmail(data, error))
        },
        setPassword: (data, error) => {
            dispatch(setPassword(data, error))
        }
    }
}

const mapStateToProps = state => {
    return {
        login: state.login
    }
}

export default connect(
    mapStateToProps,
    mapDispatchToProps
)(Login);