import React, { Component } from 'react';
import classnames from 'classnames';
import cls from './styles.module.scss';
import app_vars from '../../appConfig/app_vars.scss';

import { connect } from 'react-redux';
import {
    signupAct,
    setEmail,
    setPassword,
    setConfirmPassword,
    setFullname
}
    from '../../actions/signup';

import {
    UniRPageLayout,
    UniRForm,
    UniRContainerHorizontal,
    UniRContainerVertical,
    UniRInput,
    UniRButton,
    UniRContainer,
    UniRDropdown
} from '../../common';

import {
    Button,
    Icon,
    Menu,
    Dropdown
}
    from 'antd';
import UniRFormLayout from '../../common/UniRForm/UniRFormLayout';
import { STATUS, PATH } from '../../constant';

import Feedback from "../Feedback";

const FULLNAME_PLACEHOLDER = "Tn";
const EMAIL_REGEX = "/^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$/.";
const PASSWORD_LENGTH_VALID = " di mt khu t nht 6";
const PASSWORD_COINCIDE = "Mt khu khng khp";
const NAME_MSG_ERROR = "Tn khng c  trng";
const BIRTHDAY_MSG_ERROR = "Ngy sinh khng hp l";
const BIRTHDAY_DROPDOWN_NAME = " Ngy sinh";

class Signup extends Component {
    constructor(props) {
        super(props);
        this.state = {
            day: null,
            month: null,
            year: null,
            visible: false
        }
    }

    getResult = (chk, msg) => {
        return { result: chk, msg: msg };
    }

    onChange = (key, value, error = "") => {
        let { userPassword } = this.props.signup;

        if (key === "setConfirmPassword") {
            if (userPassword.data !== value) {
                error = PASSWORD_COINCIDE;
            }
            else {
                error = "";
            }
        }

        this.props[key](value, error);
    }

    //#region valid method
    isPasswordValid = (pass) => {
        var chkWithoutSpace = (pass.replace(/ /g, '').length >= 6);

        if (!chkWithoutSpace) return this.getResult(false, PASSWORD_LENGTH_VALID);
        else return this.getResult(true, "");
    }

    isConfirmPasswordValid(confirmPass, pass) {
        var confirmPassWithoutSpace = confirmPass.replace(/ /g, '');
        var passWithoutSpace = pass.replace(/ /g, '');

        var chkCoincide = (passWithoutSpace == confirmPassWithoutSpace);

        if (chkCoincide === false) return this.getResult(false, PASSWORD_COINCIDE);
        else return this.getResult(true, "");
    }

    isNameValid = (value) => {
        var chkWithoutSpace = (value.replace(/ /g, '').length > 0);
        if (chkWithoutSpace === false) return this.getResult(false, NAME_MSG_ERROR);
        else return this.getResult(true, "");
    }

    isBirthdayValid = () => {
        if (this.state.day === null) return this.getResult(false, BIRTHDAY_MSG_ERROR);
        else if (this.state.month === null) return this.getResult(false, BIRTHDAY_MSG_ERROR);
        else if (this.state.year === null) return this.getResult(false, BIRTHDAY_MSG_ERROR);
        else {
            var day = this.state.day;
            var month = this.state.month;
            var year = this.state.year;
            var date = new Date(month + "/" + day + "/" + year);

            if (date.getDate() === day) {
                return this.getResult(true, "");
            }
            else {
                return this.getResult(false, BIRTHDAY_MSG_ERROR);
            }
        }
    }
    //#endregion

    handleSubmit = () => {

        let { userEmail, userPassword, userConfirmPassword, fullname } = this.props.signup;

        let day = this.state.day;
        let month = this.state.month;
        let year = this.state.year;

        let date = new Date(month + "/" + day + "/" + year);

        if (date.getDate() == day) {

            let chkPass = this.isPasswordValid(userPassword.data);
            let chkfullname = this.isNameValid(fullname.data);
            let chkConfirmPassword = this.isConfirmPasswordValid(userConfirmPassword.data, userPassword.data);

            if (chkPass.result == false || chkfullname.result == false || chkConfirmPassword.result == false) {
                this.onChange("setPassword", userPassword.data, chkPass.msg);
                this.onChange("setConfirmPassword", userConfirmPassword.data, chkConfirmPassword.msg);
                this.onChange("setFullname", fullname.data, chkfullname.msg);
            } else {
                this.props.signupAct(userEmail, userPassword, fullname, date, () => {
                    this.props.history.push(PATH.LOG_IN);
                });
            }
        }
        else {
            this.getResult(false, BIRTHDAY_MSG_ERROR);
        }

    }

    onClick = (value, type = "0") => {
        switch (type) {
            case "day":
                this.setState({ day: value.value });
                break;
            case "month":
                this.setState({ month: value.value });
                break;
            case "year":
                this.setState({ year: value.value });
                break;
        }

    }

    render() {
        let days = [];
        for (var i = 1; i <= 31; i++) days.push(i);
        let months = [];
        for (var i = 1; i <= 12; i++) months.push(i);
        let years = [];
        for (var i = 2019; i >= 1900; i--) years.push(i);

        let day = this.state.day !== null ? this.state.day : "Ngy";
        let month = this.state.month !== null ? this.state.month : "Thng";
        let year = this.state.year !== null ? this.state.year : "Nm";

        let { userEmail, userPassword, userConfirmPassword, fullname, signupStatus } = this.props.signup;
        let isLoading = (signupStatus === STATUS.loading);

        let disabled = (!userEmail.data || !userPassword.data
            || !userConfirmPassword.data || !fullname.data
            || (this.state.day === null) || (this.state.month === null)
            || (this.state.year === null) || (userPassword.data !== userConfirmPassword.data));

        return (
            <UniRPageLayout>

                <UniRForm
                    className={classnames(cls.form_container)}
                    onSubmit={this.handleSubmit}
                >

                    <UniRForm.Layout title="ng k" header />

                    <UniRForm.Layout body>

                        <UniRForm.Row>

                            <UniRInput
                                type="email"
                                onChange={(value) => this.onChange("setEmail", value)}
                                label="Email"
                                value={userEmail.data}
                                error={userEmail.error}
                            />

                        </UniRForm.Row>

                        <UniRForm.Row>

                            <UniRInput
                                password
                                onChange={(value) => this.onChange("setPassword", value)}
                                label="Mt khu"
                                value={userPassword.data}
                                error={userPassword.error}
                            />

                        </UniRForm.Row>

                        <UniRForm.Row>

                            <UniRInput
                                password
                                onChange={(value) => this.onChange("setConfirmPassword", value)}
                                label="Nhp li mt khu"
                                value={userConfirmPassword.data}
                                error={userConfirmPassword.error}
                            />

                        </UniRForm.Row>

                        <UniRForm.Row>

                            <UniRInput
                                type="text"
                                onChange={(value) => this.onChange("setFullname", value)}
                                label="H tn"
                                value={fullname.data}
                                error={fullname.error}
                            />
                        </UniRForm.Row>


                        <UniRForm.Row>
                            <UniRDropdown className={classnames(cls.dropdown_wrapper)}
                                label={BIRTHDAY_DROPDOWN_NAME}
                                error={this.state.group_error}
                                labelStyle={classnames(cls.input_title)}>
                                <UniRDropdown.Group>
                                    <UniRDropdown.Item
                                        value={day}
                                        data={days}
                                        onClick={(value) => this.onClick(value, 'day')}
                                        error={this.state.item_error1}
                                    />
                                    <UniRDropdown.Item
                                        value={month}
                                        data={months}
                                        onClick={(value) => this.onClick(value, 'month')}
                                        error={this.state.item_error2}

                                    />
                                    <UniRDropdown.Item
                                        error={this.state.item_error3}
                                        value={year}
                                        data={years}
                                        onClick={(value) => this.onClick(value, 'year')}
                                    />
                                </UniRDropdown.Group>
                            </UniRDropdown>

                        </UniRForm.Row>



                    </UniRForm.Layout>

                    <UniRForm.Layout footer>
                        <UniRForm.Row className={classnames(cls.container_submit)}>
                            <UniRButton
                                containerStyle={cls.btn_submit}
                                type="primary"
                                htmlType="submit"
                                loading={isLoading}
                                disabled={disabled}
                                title={"ng k"}
                            />
                        </UniRForm.Row>

                    </UniRForm.Layout>
                </UniRForm>
            </UniRPageLayout>

        );
    }

}

const mapStateToProps = (state) => {
    return {
        signup: state.signup
    }
}

const mapDispathToProps = (dispatch) => {
    return {
        signupAct: (userEmail, userPassword, fullname, date, callbackSuccess) => {
            dispatch(signupAct(userEmail, userPassword, fullname, date, callbackSuccess));
        },

        setEmail: (data, error) => {
            dispatch(setEmail(data, error));
        },

        setPassword: (data, error) => {
            dispatch(setPassword(data, error));
        },

        setConfirmPassword: (data, error) => {
            dispatch(setConfirmPassword(data, error));
        },

        setFullname: (data, error) => {
            dispatch(setFullname(data, error));
        },
    }

}

export default connect(mapStateToProps, mapDispathToProps)(Signup);

