import React, { Component } from 'react';
import classnames from 'classnames';
import cls from './styles.module.scss';
import FlipMove from 'react-flip-move';

type UniRPageContainerProps = {
    className?: String,
    children?: React.node,
    appearAnimation?: String,
    enterAnimation?: String,
    leaveAnimation?: String,
}

class UniRPageContainer extends Component<UniRPageContainerProps> {
    constructor(props) {
        super(props);
        this.state = {};
    }
    render() {
        let {
            className,
            children,
            appearAnimation,
            enterAnimation,
            leaveAnimation,
            ...rest
        } = this.props;
        return (
            <div className={classnames(cls.container, className)}>
                <FlipMove
                    appearAnimation={appearAnimation}
                    enterAnimation={enterAnimation}
                    leaveAnimation={leaveAnimation}
                    {...rest}
                >
                    {children}
                </FlipMove>
            </div>
        );
    }
}

UniRPageContainer.defaultProps = {
    className: null,
    children: null,
    appearAnimation: "fade",
    enterAnimation: "fade",
    leaveAnimation: "fade",
}

export default UniRPageContainer;