import { API, STATUS } from '../../constant';
import { post, get } from '../../helper/axiosHelper';
import types from './types';
export const getDateFromString = (dateStr) => {
    let d = new Date(dateStr);
    d.setHours(10);
    let month = d.getUTCMonth() + 1; 
    let day = d.getUTCDate();
    let year = d.getUTCFullYear();
    let date = {
        day,
        month,
        year
    }
    return date;
}
export const getProfile = (id, callBackSuccess) => {
    return dispatch => {
        let body = {
            id: id
        }
        let url = API.PROFILE;
        post(url, body).then(
            res => {
                console.log(res);
                if (res.data.Error) {
                    console.log('111');
                    dispatch(setGetProfileStatus(STATUS.error));
                } else {
                     let userProfile = JSON.parse(res.data.ResultData);
                     console.log(userProfile);
                     let UserDOB = getDateFromString(userProfile.UserDOB);
                     dispatch(setProfile(userProfile.UserFullName,UserDOB,userProfile.UserAvatar,userProfile.UserEmail));
                     let data= {
                         userDOB: UserDOB,
                         userFullName: userProfile.UserFullName
                     }
                     callBackSuccess(data)

                }
            },
            rej => {
                console.log(rej);
                dispatch(setGetProfileStatus(STATUS.error));
            }
        )
    }
};
