import React, { PureComponent } from 'react';
import classnames from 'classnames';
import cls from './styles.module.scss';
import {
    withRouter
} from 'react-router-dom';
import {
    clearLogIn
} from '../../../../actions/login';
import {
    UniRContainerHorizontal,
    UniRButton,
    UniRContainer,
    UniRLoading
} from '../../../../common';
import { PATH, STATUS } from '../../../../constant';
import { connect } from 'react-redux';
import { getStorage } from '../../../../helper/axiosHelper';
import { Avatar, Icon } from 'antd';
import UniRDropdown from '../../../UniRDropdown';

type UserTopBarProps = {
    user?: Object,
    className?: String,
    onSignup?: Function,
    onLogin?: Function,
}

const MENU = [{
    id: 0,
    value: "Trang c nhn"
}, {
    id: 1,
    value: "i mt khu",
}, {
    id: 2,
    value: "ng xut"
}]

class UserTopBar extends PureComponent<UserTopBarProps> {
    constructor(props) {
        super(props);
        this.state = {
            userFullName: null,
            isLoggingout: false
        };
    }

    componentDidMount() {
        this.checkLogIn();
    }

    checkLogIn = () => {
        let storage = getStorage();
        console.log(storage)
        let {
            userFullName
        } = this.state;
        if (storage) {
            this.setState({
                userFullName: storage.userFullName
            })
        } else {
            if (userFullName) {
                window.location.reload();
                this.setState({
                    isLoggingout: true
                })

            }
        }
    }

    onSignup = () => {
        this.props.history.push(PATH.SIGN_UP);
    }

    onLogin = () => {
        this.props.history.push(PATH.LOG_IN);
    }

    onMenuClick = (item) => {
        switch (item.key) {
            case "0":
                this.props.history.push(PATH.PROFILE)
                break;
            case "1":
                this.props.history.push(PATH.PROFILE)
                break;
            case "2":
                this.props.clearLogIn();
                this.checkLogIn();
                break;
            default:
                console.log(item);
                break;
        }
    }


    render() {
        let {
            className
        } = this.props;
        let {
            checkVerifiedStatus,
            isVerified
        } = this.props.menuBar;
        let {
            userFullName,
            isLoggingout
        } = this.state;
        let loading = checkVerifiedStatus === STATUS.loading && <UniRLoading />;
        let verifyNoti = !isVerified &&
            <UniRContainer
                nomargin
                paddinghorrizontal
                className={classnames(cls.verify)}
            >
                <span>Ti khon ca bn cha c xc thc, vui lng kim tra li email v xc thc</span>
            </UniRContainer>
        let btn = userFullName
            ?
            <div className={classnames(
                cls.header_authen_group,
                isLoggingout && cls.loggingout
            )
            }>
                <UniRContainerHorizontal className={classnames(cls.center)}>
                    {loading}
                    <UniRContainer nomargin paddingvertical className={classnames(cls.ava)}>
                        <Avatar size={24} alt="error" src={require("../../../../assets/avatar/avatar.jpg")} />
                    </UniRContainer>
                    <UniRDropdown
                        containerStyle={classnames(cls.dropdown_container)}
                        custom
                        data={MENU}
                        dataid={"id"}
                        datavalue={"value"}
                        onClick={(item) => this.onMenuClick(item)}
                    >
                        <UniRContainerHorizontal className={classnames(cls.user_container)}>
                            <div className={classnames(cls.user)}>
                                {userFullName}
                            </div>
                            <div className={classnames(cls.icon)}>
                                <Icon type="down-circle" />
                            </div>
                        </UniRContainerHorizontal>
                    </UniRDropdown>
                </UniRContainerHorizontal>
            </div>
            :
            <div className={classnames(cls.header_authen_group)}>

                <UniRButton
                    tag
                    title="ng k"
                    btnStyle={classnames(cls.header_authen_item)}
                    onClick={() => this.onSignup()}
                />
                <UniRButton
                    tag
                    title="ng nhp"
                    btnStyle={classnames(cls.header_authen_item)}
                    onClick={() => this.onLogin()}
                />
            </div>

        return (
            <UniRContainerHorizontal className={classnames(
                cls.header_authen,
                isVerified || cls.not_verify,
                className
            )
            }>
                {verifyNoti}
                {btn}
            </UniRContainerHorizontal>

        );
    }
}

UserTopBar.defaultProps = {
    user: null,
    className: null,
    onSignup: () => { },
    onLogin: () => { }
}

const mapStateToProps = state => {
    return {
        menuBar: state.menuBar
    }
};

const mapDispatchToProps = dispatch => {
    return {
        clearLogIn: () => {
            dispatch(clearLogIn())
        }
    }
}

export default withRouter(
    connect(
        mapStateToProps,
        mapDispatchToProps
    )(UserTopBar)
);