import React, { Component } from 'react';
import classnames from 'classnames';
import cls from './styles.module.scss';

type UniRWidthOverflowProps = {
    className?: String,
    children?: React.node
}

class UniRWidthOverflow extends Component<UniRWidthOverflowProps> {
    constructor(props) {
        super(props);
        this.state = {};
    }
    render() {
        let {
            className,
            children
        } = this.props;

        return (
            <div className={classnames(cls.widthOverflow, className)}>
                {children}
            </div>
        );
    }
}

UniRWidthOverflow.defaultProps = {
    className: null,
    children: null
}

export default UniRWidthOverflow;