import React, { Component } from 'react';
import classnames from 'classnames';
import cls from './styles.module.scss';

type UniRAdHolderProps = {
    className?: String,
    width?: String,
    height?: String,
    children?: React.node
}

class UniRAdHolder extends Component<UniRAdHolderProps> {
    constructor(props) {
        super(props);
        this.state = {};
    }
    render() {
        let {
            className,
            children,
            ...rest
        } = this.props;
        return (
            <div
                className={classnames(cls.container, className)}
                {...rest}
            >
                {children}
            </div>
        );
    }
}

UniRAdHolder.defaultProps = {
    className: null,
    children: null
}

export default UniRAdHolder;