import React, { Component } from 'react';
import cls from './styles.module.scss';
import classnames from 'classnames';
import { UniRContainerVertical, UniRContainer, UniRLoading } from '../../common';
import Group from './Group';
import Input from './Input';

type UniRInputProps = {
    password?: Boolean,
    errorStyle?: String,
    labelStyle: String,
    error?: String,
    label?: String,
    type?: String,
    value?: String,
    defaultValue?: String,
    placeholder?: String,
    containerStyle?: String,
    loading?: String,
    children?: React.Node
}

class UniRInput extends Component<UniRInputProps> {
    constructor(props) {
        super(props);
        this.state = {
            value: ''
        };
    }
    static Group = Group;
    static Item = Input;
    static Password = Input.Password;

    onChange = (value) => {
        this.setState({ value });
        this.props.onChange(value);
    }

    render() {
        let {
            password,
            error,
            label,
            errorStyle,
            labelStyle,
            containerStyle,
            type,
            value,
            placeholder,
            className,
            loading,
            children
        } = this.props;
        let input =
            <Input
                password={password}
                value={value}
                onChange={(value) => this.onChange(value)}
                onInput={(value) => this.onChange(value)}
                className={classnames(cls.input, error && cls.input_error_active, className)}
                type={type}
                placeholder={placeholder}
                error={error}
            />

        return (
            <UniRContainer nomargin className={classnames(cls.input_container, children && error && cls.group_input_error_active, containerStyle)}>
                <UniRContainerVertical >
                    {label &&
                        <div className={classnames(cls.label, labelStyle)}>
                            {label}
                        </div>
                    }
                    {children || input}
                    {loading && <UniRLoading containerStyle={cls.loading} />}
                    {children && <div className={classnames("unir_error", error && "unir_error_active", errorStyle)}>
                        {error}
                    </div>}
                </UniRContainerVertical>
            </UniRContainer>
        );
    }
}

UniRInput.defaultProps = {
    password: false,
    onChange: () => { },
    error: null,
    label: null,
    errorStyle: null,
    labelStyle: null,
    type: 'text',
    value: "",
    defaultValue: "",
    placeholder: "",
    containerStyle: "",
    loading: false,
    children: null
}

export default UniRInput;