import React, { Component } from 'react';
import cls from './styles.module.scss';
import classnames from 'classnames';
import { UniRContainer } from '../../common';
import Body from './Body';
import Header from './Header';

type UniRPanelProps = {
    children?: React.Node;
    className?: String,
    nomargin?: Boolean,
    nopadding?: Boolean,
    paddinghorrizontal?: Boolean,
    paddingvertical?: Boolean,
    marginhorrizontal?: Boolean,
    marginvertical?: Boolean
}

class UniRPanel extends Component<UniRPanelProps> {
    constructor(props) {
        super(props);
        this.state = {};
    }

    static Header = Header;
    static Body = Body;

    render() {
        let {
            className,
            children,
            nomargin,
            nopadding,
            paddinghorrizontal,
            paddingvertical,
            marginhorrizontal,
            marginvertical,
            ...rest
        } = this.props;
        return (
            <UniRContainer
                className={classnames(cls.div_panel, className)}
                nomargin={nomargin}
                nopadding={nopadding}
                paddinghorrizontal={paddinghorrizontal}
                paddingvertical={paddingvertical}
                marginhorrizontal={marginhorrizontal}
                marginvertical={marginvertical}
                {...rest}
            >
                {children}
            </UniRContainer>
        );
    }
}

UniRPanel.defaultProps = {
    children: null,
    className: null,
    nomargin: false,
    nopadding: false,
    paddinghorrizontal: false,
    paddingvertical: false,
    marginhorrizontal: false,
    marginvertical: false
}

export default UniRPanel;