import React, { Component } from "react";
import { Rate, Input } from "antd";
import { post, getStorage } from '../../helper/axiosHelper';
import classnames from 'classnames';
import cls from './styles.module.scss';
import swal from 'sweetalert';

import { PATH, STATUS } from "../../constant";
import { connect } from "react-redux";

import {
    feedbackAct
}
    from "../../actions/feedback";

import {
    UniRModal,
    UniRInput,
    UniRContainerVertical,
    UniRContainer
} from "../../common"

class Feedback extends Component {

    constructor(props) {
        super(props);
        this.state = {
            rate: 5,
            feedbackContent: "",
        }
    }

    handleOk = () => {

        let { rate, feedbackContent } = this.state;

        let UserID = getStorage().userID;
        if (UserID === undefined) {

            swal({
                title: "Li!",
                button: "OK!",
                icon: "error",
                className: "swal-btn",
                text: "Bn phi ng nhp  thc hin chc nng ny"
            }).then((value) => {
                
                });

            return;

        }

        this.props.feedbackAct(rate, feedbackContent, (data) => {
            this.handleCancel();


            let { feedbackStatus } = this.props.feedback;

            console.log(feedbackStatus);
            console.log(data);

            if (feedbackStatus == STATUS.error) {
 
                swal({
                    title: "Li!",
                    button: "OK!",
                    icon: "error",
                    className: "swal-btn",
                    text: "Bn phi ng nhp  thc hin chc nng ny"
                });
            }
            else {
                swal({
                    title: "Cm n!",
                    button: "OK!",
                    icon: "success",
                    className: "swal-btn"
                });
            }

        });
    }

    handleCancel = () => {
        this.setState({ feedbackContent: "" });
        this.props.onCancel();
    }

    onRateChange(value) {
        this.setState({ rate: value });
    }

    onTextareaType = (e) => {
        this.setState({ feedbackContent: e.target.value });
    }

    render() {
        let visible = this.props.visible;
        let rate = this.state.rate;
        let feedbackContent = this.state.feedbackContent;
        let disabled = (this.state.feedbackContent.replace(/ /g, '').length == 0);

        let { feedbackStatus } = this.props.feedback;
        let isLoading = (feedbackStatus === STATUS.loading);

        return (
            <UniRModal
                title="Gi nh gi v phn hi"
                oktitle="Gi"
                visible={visible}
                onOk={this.handleOk}
                onCancel={this.handleCancel}
                disabled={disabled}
                loading={isLoading}
            >

                <UniRContainerVertical>
                    <UniRContainer nopadding nomargin>
                        <Rate
                            value={rate}
                            onChange={(e) => this.onRateChange(e)}
                            className={classnames(cls.rate)}
                        />
                        <p className={classnames(cls.evaluateText)}> Click  nh gi </p>

                    </UniRContainer>



                    <UniRContainer nopadding nomargin>
                        <Input.TextArea
                            rows={5}
                            value={feedbackContent}
                            onChange={(e) => this.onTextareaType(e)}
                            placeholder="Vit nhn xt..."
                        />
                    </UniRContainer>

                </UniRContainerVertical>
            </UniRModal>
        );
    }
}

const mapStateToProps = (state) => {
    return {
        feedback: state.feedback
    }
}

const mapDispathToProps = (dispatch) => {
    return {
        feedbackAct: (rate, feedbackContent, cb) => {
            dispatch(feedbackAct(rate, feedbackContent, cb))
        }
    }
}

export default connect(mapStateToProps, mapDispathToProps)(Feedback);