import { API, STATUS } from '../../constant';
import { post, getStorage } from '../../helper/axiosHelper';
import types from './types';

export const feedbackAct = ( rate, feedbackContent, callbackSuccess) => {
    return dispatch => {

        let UserID = getStorage().userID;
        
        let body = {
            entityName: 'Feedback',
            masterData: JSON.stringify({
                FeedbackID: 100,
                UserID: UserID,
                FeedbackPoint: rate,
                FeedbackContent: feedbackContent
            }),
            mode: 'New'
        }

        let url = API.FEEDBACK;
        dispatch(setFeedbackStatus(STATUS.loading))
        post(url, body).then(
            res => {
                if (res.data.Error) {
                    dispatch(setFeedbackStatus(STATUS.error))
                }
                else {
                    try{
                        let data = JSON.parse(res.data.ResultData);
                        dispatch(setFeedbackStatus(STATUS.success));
                        callbackSuccess(data);
                    }
                    catch(e){
                        dispatch(setFeedbackStatus(STATUS.error))
                        
                    }
                    
                }
            },
            rej => {
                dispatch(setFeedbackStatus(STATUS.error))
            }
        ) 
    }
}

export const setFeedbackStatus = (status) => ({
    type: types.SET_FEEDBACK_STATUS,
    status
})