import React, { PureComponent } from 'react';
import cls from './styles.module.scss';
import classnames from 'classnames';
import { Input as AntdInput } from 'antd';
import {
    UniRContainer,
    UniRContainerVertical,
    UniRLoading
} from '../../../common';

type InputProps = {
    password?: Boolean,
    errorStyle?: String,
    labelStyle: String,
    error?: String,
    label?: String,
    type?: String,
    value?: String,
    defaultValue?: String,
    placeholder?: String,
    loading?: Boolean
}

class Input extends PureComponent<InputProps> {
    constructor(props) {
        super(props);
        this.state = {
            value: ''
        };
    }

    static Password = AntdInput.Password;

    onChange = (value) => {
        this.setState({ value });
        this.props.onChange(value);
    }

    render() {
        let {
            password,
            error,
            label,
            errorStyle,
            labelStyle,
            type,
            value,
            placeholder,
            className,
            loading
        } = this.props;
        let input = password ?
            <AntdInput.Password
                value={value}
                onChange={(e) => this.onChange(e.target.value)}
                onInput={(e) => this.onChange(e.target.value)}
                className={classnames(cls.input, error && cls.input_error_active, className)}
                type={type}
                placeholder={placeholder}
            />
            :
            <AntdInput
                onChange={(e) => this.onChange(e.target.value)}
                onInput={(e) => this.onChange(e.target.value)}
                value={value}
                className={classnames(cls.input, error && cls.input_error_active, className)}
                type={type}
                placeholder={placeholder}
            />
        return (
            <UniRContainer nomargin nopadding>
                <UniRContainerVertical >
                    {label &&
                        <div className={classnames(cls.label, labelStyle)}>
                            {label}
                        </div>
                    }
                    {input}
                    {loading && <UniRLoading containerStyle={cls.loading} />}
                    <div className={classnames("unir_error", error && "unir_error_active", errorStyle)}>
                        {error}
                    </div>
                </UniRContainerVertical>
            </UniRContainer>
        );
    }
}

Input.defaultProps = {
    password: false,
    onChange: () => { },
    error: null,
    label: null,
    errorStyle: null,
    labelStyle: null,
    type: 'text',
    value: "",
    defaultValue: "",
    placeholder: "",
    loading: false
}

export default Input;