import React, { PureComponent } from 'react';
import classnames from 'classnames';
import cls from './styles.module.scss';
import {
    UniRContainerHorizontal,
    UniRContainerVertical,
    UniRAdHolder,
} from '../../../common';
import Img from 'react-image';

type UniRBodyProps = {
    children?: React.Node,
    className?: String,
}

class UniRBody extends PureComponent<UniRBodyProps> {
    constructor(props) {
        super(props);
        this.state = {};
    }
    render() {
        let {
            children,
            className
        } = this.props;
        return (
            <UniRContainerHorizontal className={classnames(cls.body_container, className)}>
                <UniRAdHolder>
                    <Img alt="Qung co" src={require('../../../assets/adv/vertical/1.png')} />
                </UniRAdHolder>
                <UniRContainerVertical className={classnames(cls.main_content_container)}>
                    {children}
                </UniRContainerVertical>
                <UniRAdHolder>
                    <Img alt="Qung co" src={require('../../../assets/adv/vertical/2.png')} />
                </UniRAdHolder>
            </UniRContainerHorizontal>
        );
    }
}

UniRBody.defaultProps = {
    children: null,
    className: null,
}

export default UniRBody;