import React, { PureComponent } from 'react';
import cls from './styles.module.scss';
import classnames from 'classnames';
import {
    UniRContainer,
    UniRContainerHorizontal,
    UniRPanel,
    UniRContainerVertical,
    UniRButton
} from '../../../common';
import { UniRImage } from '../../../common';
import { FaPollH, FaMapMarkerAlt } from 'react-icons/fa';

class UniRow extends PureComponent {
    constructor(props) {
        super(props);
        this.state = {};
    }

    onTagClick = (e, key, value) => {
        e.stopPropagation();
        this.props.onTagClick(key, value);
    }

    onPointTagClick = (key, value) => {
        this.props.onTagClick(key, value);
    }

    render() {
        let { data } = this.props;
        return (
            <UniRContainer nomargin paddingvertical >
                <UniRPanel nomargin paddingvertical className={classnames(cls.unirow_panel)}>
                    <UniRContainerHorizontal>
                        <UniRPanel marginhorrizontal paddingvertical className={classnames("col-3")}>
                            <UniRImage
                                className={classnames(cls.img)}
                                src={require("../../../assets/uni_logo/fpt.png")}
                                lightbox
                            />
                        </UniRPanel>
                        <UniRContainer nomargin paddinghorrizontal className={classnames(cls.content)}>
                            <UniRContainerVertical>
                                <div className={classnames(cls.title)}>
                                    {data.UniversityName}
                                </div>
                                <div style={{ WebkitBoxOrient: 'vertical' }} className={classnames(cls.short_des)}>
                                    {data.UniversityShortDescription}
                                </div>
                                <UniRContainerHorizontal className={classnames(cls.footer)}>
                                    <UniRContainerHorizontal className={classnames(cls.tag_container)}>
                                        <UniRButton
                                            btnStyle={classnames(cls.tag_modify)}
                                            onClick={() => this.onPointTagClick(
                                                ["pointFrom", "pointTo"],
                                                [data.UniversityPointFrom, data.UniversityPointTo]
                                            )}
                                        >
                                            <UniRContainer nomargin nopadding className={classnames(cls.icn)}>
                                                <FaPollH />
                                            </UniRContainer>
                                            <span
                                                ref={(inst) => this.pointFrom = inst}
                                                className={classnames(cls.point)}
                                                onClick={(e) => this.onTagClick(e, "pointFrom", data.UniversityPointFrom)}
                                            >
                                                {data.UniversityPointFrom}
                                            </span>
                                            ~
                                            <span
                                                ref={(inst) => this.pointTo = inst}
                                                className={classnames(cls.point)}
                                                onClick={(e) => this.onTagClick(e, "pointTo", data.UniversityPointTo)}

                                            >
                                                {data.UniversityPointTo}
                                            </span>
                                            {!data.UniversityPointFrom && <span>-</span>}
                                        </UniRButton>
                                        <UniRButton
                                            btnStyle={classnames(cls.tag_modify)}
                                            title={data.CityName}
                                            onClick={(e) => this.onTagClick(e, "city", { key: data.CityID, value: data.CityName })}
                                        >
                                            <UniRContainer nomargin nopadding className={classnames(cls.icn)}>
                                                <FaMapMarkerAlt />
                                            </UniRContainer>
                                        </UniRButton>
                                    </UniRContainerHorizontal>
                                    <UniRContainer nomargin nopadding className={classnames(cls.btn_padding)}>
                                        <UniRButton
                                            btnStyle={classnames(cls.uni_btn)}
                                            title="Xem chi tit >>"
                                        />
                                    </UniRContainer>

                                </UniRContainerHorizontal>
                            </UniRContainerVertical>
                        </UniRContainer>
                    </UniRContainerHorizontal>
                </UniRPanel>
            </UniRContainer>
        );
    }
}

export default UniRow;