import { API, STATUS } from '../../constant';
import { BACKEND_URL } from '../../appConfig/index';
import { post} from '../../helper/axiosHelper';
import types from './types';
export const changePasswordAct = (userPassword,userNewPassword,userEmail, callBackSuccess) => {
    return dispatch => {
        let checkUserUrl = API.CHANGE_PASSWORD;
        console.log(checkUserUrl);
        let body = {
            userEmail,
            userPassword,
            userNewPassword
        }
        dispatch(setStatus(STATUS.loading));
        post(checkUserUrl, body).then(res => {
            let data = JSON.parse(res.data.ResultData);
            if (res.data.Error) {
                console.log('error change');
                dispatch(setStatus(STATUS.error))
                // dispatch(setEmail(userEmail.data, res.data.Message))
            } else {
                console.log('success change');
                console.log(res);
                dispatch(setStatus(STATUS.success));
                dispatch(setPassword("",""));
                dispatch(setNewPassword("",""));
                dispatch(setRePassword("",""));
            }
            callBackSuccess(res);
        }).catch(rej => {
            console.log('erorrrr')
            dispatch(setStatus(STATUS.error));
        });
        }
};
export const setStatus = (status) => ({
    type: types.SET_STATUS,
    status
})
export const setPassword = (data = "", error = "") => ({
    type: types.SET_PASSWORD,
    password: { data, error }
});
export const setNewPassword = (data = "", error = "") => ({
    type: types.SET_NEW_PASSWORD,
    newPassword: { data, error }
});
export const setRePassword = (data = "", error = "") => ({
    type: types.SET_RE_PASSWORD,
    rePassword: { data, error }
});