import { STATUS } from '../constant/status';
import types from '../actions/profile/types';

const initState = {
    userFullName: "",
    userId: "",
    userEmail: "",
    day: "1",
    month: "1",
    year: "2000",
    userAvatar: "",
    userStatus: STATUS.default
}

const profile = (state = initState, action) => {
    switch (action.type) {
        case types.SET_AVATAR:
            return {
                ...state,
                avatar: action.avatar
            }
        case types.SET_PROFILE:
            return {
                ...state,
                userFullName: action.userFullName,
                day: action.day,
                month: action.month,
                year: action.year,
                userAvatar: action.userAvatar,
                userEmail: action.userEmail
            }
        case types.SET_FULL_NAME:
            return {
                ...state,
                userFullName: action.userFullName
            }
        // case types.SET_PASSWORD:
        //     return {
        //         ...state,
        //         password: action.password
        //     }
        // case types.SET_NEW_PASSWORD:
        // return {
        //     ...state,
        //     newPassword: action.newPassword
        // }
        // case types.SET_RE_PASSWORD:
        // return {
        //     ...state,
        //     rePassword: action.rePassword
        // }
        default:
            return state;
    }
}
export default profile