import React, { Component } from "react";
import classnames from 'classnames';
import cls from './styles.module.scss';
import { post } from '../../helper/axiosHelper';
import { API } from '../../constant';
import {
    UniRContainerVertical,
    UniRContainerHorizontal,
    UniRPageLayout,
    UniRImage,
    UniRContainer,
    UniRPanel
} from '../../common';
const entityName = 'Article';

class BlogDetail extends Component {
    constructor(props) {
        super(props);
        this.state = {
            id: 1,
            title: '',
            shortDescription: '',
            image: '',
            content: '',
            author: '',
            createDate: '',
            pageError: false,
            errorMessage: ''

        }
    }
    formatDate = (num) => {
        let fn = '0' + num;
        return fn.slice(-2);
    }
    getDateString = (dateStr) => {
        let d = new Date(dateStr);
        d.setHours(10);
        let month = d.getUTCMonth() + 1; 
        let day = d.getUTCDate();
        let year = d.getUTCFullYear();
        return this.formatDate(day) + '/' + this.formatDate(month) + '/' + year;
    }
    componentDidMount() {
        let { id } = this.props.match.params;
        if ( !isNaN(id) ) {
            this.setState({
                id: id
            });
            let getArticleUrl = API.GET_ARTICLE;
            let body = {
                id: id,
                entityName: entityName
            }
            post(getArticleUrl, body).then(res => {
                let article = JSON.parse(res.data.ResultData);
                if (article) {
                    console.log(article);
                    let date = new Date(article.CreatedDate);
                    date.setHours(10);
                    let createDate = this.getDateString(date);
                    this.setState({
                        title: article.ArticleTitle,
                        shortDescription: article.ArticleShortDescription,
                        image: article.ArticleImages,
                        content: article.ArticleContent,
                        author: article.ArticleAuthor,
                        createDate: createDate
                    });
                } else {
                    this.handleError('Khng tm thy bi vit');
                }
                
            }).catch( rej => {
                
                this.handleError('Khng tm thy bi vit');
            });
        } else {
            this.handleError('Khng tm thy bi vit');
        }

    }
    handleError = (errorMessage) => {
        this.setState({
            pageError: true,
            errorMessage
        })
    }
    render(){
        let {
            title,
            shortDescription,
            image,
            content,
            author,
            createDate,
            pageError,
            errorMessage
        } = this.state;
        return(
            (pageError === true) ? <UniRPageLayout>{errorMessage}</UniRPageLayout>
            : <UniRPageLayout>
                <UniRPanel>
                    <UniRPanel.Header title={title} className={cls.blog_title}/>
                    <UniRPanel.Body>
                        <UniRContainerVertical>
                            <div className={classnames(cls.blog_description)}>{shortDescription}</div>
                            <div className={cls.blog_image}>
                                <UniRImage src={image}
                                 lightbox={true} 
                                 />
                            </div>
                            <div className={cls.blog_content}>{content}</div>
                            <UniRContainerHorizontal className={cls.blog_info}>
                                <div className={cls.blog_author}>{author}</div>
                            </UniRContainerHorizontal>
                            <UniRContainerHorizontal className={cls.blog_info}>
                                <div className={cls.blog_date}>{createDate}</div>
                            </UniRContainerHorizontal>
                        </UniRContainerVertical>
                    </UniRPanel.Body>
                    
                </UniRPanel>
            </UniRPageLayout>
        );
    }
}
export default BlogDetail;