import React, { PureComponent } from 'react';
import classnames from 'classnames';
import cls from './styles.module.scss';
import {
    Dropdown as AntdDropdown,
    Button,
    Menu,
    Icon
} from 'antd';
import { UniRContainer, UniRContainerVertical, UniRLoading } from '../../../common';

type DropdownProps = {
    data?: Array,
    dataid?: String,
    datavalue?: String,
    value?: String,
    onClick?: Function,
    value?: String,
    label?: String,
    error?: String,
    labelStyle?: String,
    errorStyle?: String,
    loading?: Boolean,
    children?: React.Node
}

class Dropdown extends PureComponent<DropdownProps> {
    constructor(props) {
        super(props);
        this.state = {};
    }

    onClick = (e) => {
        this.props.onClick({ key: e.key, value: e.item.props.children[1] });
    }

    render() {
        let {
            data,
            dataid,
            datavalue,
            value,
            label,
            error,
            errorStyle,
            labelStyle,
            loading,
            children
        } = this.props;
        let items =
            <Menu onClick={this.onClick}>
                {
                    data.map((d, i) => (
                        <Menu.Item key={dataid ? d[dataid] : i} > {datavalue ? d[datavalue] : d} </Menu.Item>
                    ))
                }
            </Menu >
        return (
            <UniRContainer nomargin nopadding>
                <UniRContainerVertical >

                    {label &&
                        <div className={classnames(cls.label, labelStyle)}>
                            {label}
                        </div>
                    }
                    <AntdDropdown
                        className={classnames(error && cls.dropdown_error_active)}
                        trigger={['click']}
                        overlayClassName={classnames(cls.overlay, data.length !== 0 && cls.hasdata)}
                        overlay={items}
                    >
                        {children || <Button
                            className={classnames(cls.btn)}
                        >
                            {value}
                            <span>
                                {loading ?
                                    <UniRLoading /> :
                                    <Icon type="down" />}
                            </span>
                        </Button>}
                    </AntdDropdown>
                    <div className={classnames("unir_error", error && "unir_error_active", errorStyle)}>
                        {error}
                    </div>
                </UniRContainerVertical >
            </UniRContainer>

        );
    }
}

Dropdown.defaultProps = {
    data: [],
    dataid: "",
    datavalue: "",
    value: "",
    onClick: () => { },
    label: "",
    error: "",
    labelStyle: null,
    errorStyle: null,
    loading: false,
    children: null
}

export default Dropdown;