import { API, STATUS } from '../../constant';
import { post, get } from '../../helper/axiosHelper';
import types from './types';

export const loginAct = (userEmail, userPassword, callBackSuccess) => {
    return dispatch => {
        let body = {
            userEmail: userEmail.data,
            userPassword: userPassword.data,
        }
        let url = API.LOG_IN;
        dispatch(setLoginStatus(STATUS.loading))
        post(url, body).then(
            res => {
                console.log(res)

                if (res.data.Error) {
                    dispatch(setLoginStatus(STATUS.error))
                    dispatch(setEmail(userEmail.data, res.data.Message))
                } else {
                    let data = JSON.parse(res.data.ResultData);
                    console.log(data);
                    dispatch(clearLogIn());
                    dispatch(setLoginStatus(STATUS.success))
                    dispatch(setUser(
                        data.UserID,
                        data.UserEmail,
                        data.UserFullName,
                    ))
                    dispatch(setStorage(
                        data.UserID,
                        data.UserToken,
                        data.UserEmail,
                        data.UserFullName,
                        data.IsVerified
                    ))
                    callBackSuccess(data);
                }
            },
            rej => {
                dispatch(setLoginStatus(STATUS.error))
            }
        )
    }
}

export const setLoginStatus = (status) => ({
    type: types.SET_LOG_IN_STATUS,
    status
})

export const setEmail = (data = "", error = "") => ({
    type: types.SET_EMAIL,
    userEmail: { data, error }
})

export const setPassword = (data = "", error = "") => ({
    type: types.SET_PASSWORD,
    userPassword: { data, error }
})

export const setUser = (userID, userEmail, userFullName) => ({
    type: types.SET_USER,
    userID,
    userEmail,
    userFullName
})

export const setStorage = (userID, userToken, userEmail, userFullName, isVerified) => ({
    type: types.SET_STORAGE,
    userID,
    userToken,
    userEmail,
    userFullName,
    isVerified
})

export const clearLogIn = () => ({
    type: types.CLEAR_LOG_IN
})