import { STATUS } from '../constant'
import types from '../actions/feedback/types'

const initState = {
    rate: 5,
    feedbackContent: "",
    feedbackStatus: STATUS.default,
    feedbackError : ""
}

const feedback = (state = initState, action) => {
    switch (action.type) {
        case types.SET_FEEDBACK_STATUS:
            return {
                ...state,
                feedbackStatus: action.status,
                feedbackError: action.error
            }
        default:
            return state
    }
}

export default feedback