import React, { PureComponent } from 'react';
import cls from './styles.module.scss';
import classnames from 'classnames';
import { UniRContainer } from '../../../common';

type BodyProps = {
    children?: CanvasRect.Node,
    className?: String
}

class Body extends PureComponent<BodyProps> {
    constructor(props) {
        super(props);
        this.state = {};
    }
    render() {
        let {
            className,
            children
        } = this.props;
        return (
            <UniRContainer nomargin className={classnames(cls.body, className)}>
                {children}
            </UniRContainer>
        );
    }
}

Body.defaultProps = {
    children: null,
    className: null
}

export default Body;