import React, { PureComponent } from 'react';
import classnames from 'classnames';
import cls from './styles.module.scss';
import { UniRContainerHorizontal } from '../../../common';

type GroupProps = {
    children?: React.Node,
}

class Group extends PureComponent<GroupProps> {
    constructor(props) {
        super(props);
        this.state = {};
    }

    render() {
        let {
            children
        } = this.props;
        return (
            <UniRContainerHorizontal className={classnames(cls.group)}>
                {children}
            </UniRContainerHorizontal>
        );
    }
}

Group.defaultProps = {
    children: null
}

export default Group;