import store from "..";
import axios from 'axios';
import { PATH } from '../constant';
import {
    LS_KEY,
    CONFIG_PATH,
    BACKEND_URL_AKA
} from '../appConfig';
import qs from 'qs';
import secureLs from "secure-ls";
var ls = new secureLs();

export const getConfig = () => {
    return axios.get(CONFIG_PATH).then(
        res => {
            return handleRespond(res)
        }
    ).catch(err => handleRej(err))
}

export const post = (url, body) => {
    console.log(addToken(body))
    return getConfig().then(
        res => {
            let { BACKEND_URL } = res.data;

            url = url.replace(BACKEND_URL_AKA, BACKEND_URL);

            return axios(url, {
                method: "POST",
                headers: {
                    'Accept': 'application/json',
                    "Content-type": "application/x-www-form-urlencoded",
                },
                data: qs.stringify(addToken(body))
            }).then(
                res => {
                    return handleRespond(res);
                }
            ).catch(err => {
                return handleRej(err)
            })
        })

}

