export const get = (url) => {
    return axios(url, {
        method: "get",
        headers: {
            'Accept': 'application/json',
            "Content-type": "application/json",
        }
    }).then(
        res => {
            return handleRespond(res);
        }
    ).catch(err => {
        return handleRej(err);
    });
}

const handleRespond = (res, isImg = false) => {
    return new Promise((resolve, reject) => {

        switch (res.status) {
            case 400:
                console.log(res)
                reject()
                break;
            case 401:
                let username = localStorage.getItem('username');
                let userType = store.getState().login.userType;
                localStorage.setItem(username, userType);
                window.location.href = PATH.BASE_NAME;
                reject()
                break;
            case 415:
                console.log(res)
                reject()
                break;
            case 500:
                console.log(res)
                reject()
                break;
            case 200:
                if (isImg) {
                    resolve({ data: `data:image/png;base64,${Buffer.from(res.data, 'binary').toString('base64')}` });
                } else {
                    resolve(res)
                }
                break;
            default:
                res.json().then(json => {
                    reject()
                    return json;
                }).catch(err => {
                    reject()
                    return err;
                })

        }
    })
}

const handleRej = (err) => {
    return new Promise((resolve, reject) => {
        console.log(err)
        // window.location.href = `${window.location.protocol}${PATH.ERROR_PAGE}?err=${err}`;
        reject(err);
        return err.message;
    })
}


const addToken = (body) => {
    let storage = ls.get(LS_KEY);
    let userToken = null;
    if (storage) {
        userToken = storage.userToken;
    }
    body.userToken = userToken;
    return body;
}

export const getStorage = () => {
    return ls.get(LS_KEY);
}

export const setStorage = (value) => {
    return ls.set(LS_KEY, value);
}