import React, { Component } from 'react';
import classnames from 'classnames';
import cls from './styles.module.scss';
import { UniRContainerVertical, UniRWidget } from '../../common';
import UniRFormRow from './UniRFormRow';
import UniRFormLayout from './UniRFormLayout';

type UniRFormProps = {
    title?: String,
    children?: React.Node,
    onSubmit?: Function,
    className?: String
}


class UniRForm extends Component<UniRFormProps> {
    constructor(props) {
        super(props);
        this.state = {};
    }
    static Row = UniRFormRow;
    static Layout = UniRFormLayout;

    handleSubmit = (e) => {
        e.preventDefault();
        this.props.onSubmit();
    }

    render() {
        let {
            title,
            children,
            className
        } = this.props;
        return (
            <form
                onSubmit={this.handleSubmit}
                className={classnames(cls.form_container, className)}
            >
                <UniRWidget className={classnames(cls.form_content)}>
                    <UniRContainerVertical>
                        {children}
                    </UniRContainerVertical>
                </UniRWidget>

            </form>

        );
    }
}

UniRForm.defaultProps = {
    title: "",
    children: null,
    onSubmit: () => { },
    className: null
}

export default UniRForm;