import React, { PureComponent } from 'react';
import classnames from 'classnames';
import cls from './styles.module.scss';
import Slideshow from './Slideshow';
import UserTopBar from './UserTopBar';
import MenuBar from './MenuBar';
import {
    UniRWidthOverflow,
    UniRContainerVertical
} from '../../../common';

type UniRHeaderProps = {
    className?: String,
    user?: Object,
    imgSlideShow?: Array,
    logo?: React.ElementType,
    logoHorizontal?: React.ElementType
}

const MIN_HEIGHT_ACTIVE_NAV_BAR = 180;
const MIN_HEIGHT_HIDE_NAV_BAR = 240;
const NAV_BAR_TIME_OUT = 2000;
const LOGO = require('../../../assets/logo/logo.png');
const LOGO_HORIZONTAL = require('../../../assets/logo/logo_horizontal.png');

class UniRHeader extends PureComponent<UniRHeaderProps> {
    constructor(props) {
        super(props);
        this.state = {
            isActiveNavbar: false,
            isHideNavbar: false,
        };
        this.handleScroll = this.handleScroll.bind(this);
        this.currentPosY = window.scrollY;
        this.handleNavBarTimeOut = null;
    }

    componentDidMount() {
        window.addEventListener('scroll', this.handleScroll);
    }

    componentWillUnmount() {
        window.removeEventListener('scroll', this.handleScroll);
    }

    handleScroll = (e) => {
        let { imgSlideShow } = this.props;
        let tempMinHide = MIN_HEIGHT_HIDE_NAV_BAR;
        let tempMinActive = MIN_HEIGHT_ACTIVE_NAV_BAR;
        if (imgSlideShow.length === 0) {
            tempMinHide /= 4;
            tempMinActive /= 4;
        }
        let posY = window.scrollY;
        if (posY >= tempMinActive) {
            this.setState({ isActiveNavbar: true });
            clearTimeout(this.handleNavBarTimeOut);
            if (posY >= tempMinActive && posY <= tempMinHide) {
                this.setState({ isHideNavbar: false });
            } else {
                if (posY > this.currentPosY) {
                    this.setState({ isHideNavbar: true });
                } else {
                    this.setState({ isHideNavbar: false });
                    this.handleNavBarTimeOut = setTimeout(() => {
                        this.setState({ isHideNavbar: true })
                    }, NAV_BAR_TIME_OUT);
                }
            }

        } else {
            this.setState({ isActiveNavbar: false })
        }
        this.currentPosY = posY;
    }

    render() {
        let {
            imgSlideShow,
            logo,
            logoHorizontal,
            className
        } = this.props;

        let {
            isActiveNavbar,
            isHideNavbar
        } = this.state;

        return (
            <UniRContainerVertical className={classnames(imgSlideShow.length === 0 && cls.header_container, className)}>
                <UniRWidthOverflow>
                    <UserTopBar
                        className={classnames(
                            cls.user_top_bar_container,
                            isActiveNavbar && isHideNavbar && cls.nav_bar_hide
                        )}
                    />
                </UniRWidthOverflow>
                <MenuBar
                    className={classnames(
                        cls.user_top_bar_container,
                        (isActiveNavbar || imgSlideShow.length === 0) && cls.nav_bar_active,
                        isActiveNavbar && isHideNavbar && cls.nav_bar_hide
                    )}
                    transparent={imgSlideShow.length !== 0 && true}
                    logo={(isActiveNavbar || imgSlideShow.length === 0) ? logoHorizontal : logo}
                />
                {
                    imgSlideShow.length !== 0 && <UniRWidthOverflow>
                        <Slideshow
                            images={imgSlideShow}
                        />
                    </UniRWidthOverflow>
                }
            </UniRContainerVertical>
        );
    }
}

UniRHeader.defaultProps = {
    className: null,
    user: null,
    imgSlideShow: [],
    logo: LOGO,
    logoHorizontal: LOGO_HORIZONTAL
}

export default UniRHeader;